import React from "react";
import styled from "styled-components";
import { useHistory } from "react-router";
import expireIcon from "../../assets/img/expireIcon.svg";
const Styled = {
  Wrapper: styled.div`
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 64px;
      height: 64px;
    }
    .title {
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
    .content {
      font-size: 20px;
      margin-top: 20px;
    }
    .reset {
      font-size: 20px;
      color: #2260e8;
      margin-top: 60px;
      :hover {
        text-decoration: underline;
        cursor: pointer;
      }
    }
  `,
};
export default function Expired() {
  const history = useHistory();
  const onClick = () => {
    history.push("/");
    window.localStorage.clear();
    window.sessionStorage.clear();
  };
  return (
    <Styled.Wrapper>
      <div>
        <img src={expireIcon} alt="expiredIcon" />
        <div className="title">Login Session Expired</div>
        <div className="content">
          Your session timed out. To continue working, sing in agin
        </div>
        <div className="reset" onClick={onClick}>
          Go to the sign-in page
        </div>
      </div>
    </Styled.Wrapper>
  );
}
