import styled from "styled-components";
import IconImg from "../../components/IconImg";
import LoginInput from "../../components/LoginInput";
import { colors } from "../../style/color";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import useForm from "../../utils/useForm";
import { PATH } from "../../assets/data/common";
import { apiPostLoginLogis } from "../../utils/api";
import { makeCryptoFunction } from "../../utils/crypto";
import { jwtSign } from "../../utils/jwt";
import loginImg from "../../assets/img/loginPage.svg";
import PortalModal from "../../components/modal/Portal";
import { Check } from "../../components/modal/custom";
const Styled = {
  Root: styled.div`
    display: grid;
    grid-template-columns: 3fr 2fr;
    height: 100vh;
  `,
  LeftBody: styled.div`
    background-color: ${colors.loginPageBackground};
  `,
  LoginImg: styled.img`
    width: 100%;
    height: 100%;
    max-height: 100vh;
  `,
  RightBody: styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  `,
  LoginForm: styled.form`
    display: flex;
    flex-direction: column;
    width: 57%;
  `,
  PasswordPart: styled.div`
    position: relative;
  `,

  KeepLoggedin: styled.div`
    margin-top: 70px;
    display: flex;
    align-items: center;
    .MuiSvgIcon-root {
      color: ${(props) =>
        props.clickLoggedin === true
          ? colors.buttonBackground
          : colors.platinum};
      :hover {
        cursor: pointer;
      }
    }
    p {
      margin-left: 10px;
      color: ${(props) =>
        props.clickLoggedin === true
          ? colors.buttonBackground
          : colors.platinum};
      :hover {
        cursor: pointer;
      }
    }
  `,
  LoginButton: styled.button`
    background-color: ${colors.buttonBackground};
    color: ${colors.white};
    border: none;
    width: 100%;
    height: 60px;
    font-size: 20px;
    margin-top: 20px;
  `,
  ForgotButton: styled.div`
    position: absolute;
    right: 20px;
    top: 20px;
    border: none;
    background-color: ${colors.loginPageBackground};
    color: ${colors.buttonBackground};
    text-decoration: underline;
    font-size: 16px;
    :hover {
      cursor: pointer;
    }
  `,
  TypeButtonWrapper: styled.div`
    margin-top: 60px;
    width: 57%;
  `,
};

export default function Login() {
  const [clickLoggedin, setClickLoggedin] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const history = useHistory();
  const keepLoggedIn = () => {
    setClickLoggedin(!clickLoggedin);
  };

  const { values, handleChange, handleSubmit } = useForm({
    initialValues: { id: "", password: "" },
    onSubmit: async (values) => {
      const form = new FormData();
      form.append("email", values.id);
      // form.append("password", values.password);
      // crypto
      const cryptoedPassword = makeCryptoFunction(values.password);
      form.append("password", cryptoedPassword);
      try {
        const res = await apiPostLoginLogis(form);
        getUserData(res);
      } catch (err) {
        setModalVisible(true);
      }
    },
  });
  const getUserData = async (datas) => {
    try {
      const userId = datas.data.userid;
      const accessToken = datas.data.accesstoken;
      const is_manager = datas.data.is_manager;
      const encodedData = {
        userid: userId,
        is_manager: is_manager,
        accesstoken: accessToken,
      };
      const userData = jwtSign(encodedData);
      switch (clickLoggedin) {
        case true:
          localStorage.setItem("userData", userData);
          break;
        default:
          sessionStorage.setItem("userData", userData);
      }
      history.push(PATH.dashboard);
    } catch (err) {}
  };
  const modalCont = {
    check: (
      <Check
        title="COCO Truck"
        info="Wrong Check your E-mail(ID) or Password again"
        onCancel={() => setModalVisible(false)}
      />
    ),
  };

  return (
    <Styled.Root>
      {modalVisible && (
        <PortalModal visible={modalVisible} setVisible={setModalVisible}>
          {modalCont.check}
        </PortalModal>
      )}
      <Styled.LeftBody>
        <Styled.LoginImg src={loginImg} alt="login Page" />
      </Styled.LeftBody>
      <Styled.RightBody>
        <IconImg imgType="loginIcon" width="35%" height="auto" />
        <Styled.LoginForm onSubmit={handleSubmit}>
          <LoginInput
            backgroundcolor={colors.loginPageBackground}
            width="100%"
            height="60px"
            margin="80px 0 40px 0"
            padding="0 20px 0 20px"
            fontSize="16px"
            name="id"
            type="id"
            onChange={handleChange}
            value={values.id}
            placeholder="E-mail"
          />
          <Styled.PasswordPart>
            <LoginInput
              backgroundcolor={colors.loginPageBackground}
              width="100%"
              height="60px"
              padding="0 20px 0 20px"
              fontSize="16px"
              name="password"
              type="password"
              onChange={handleChange}
              value={values.password}
              placeholder="Password"
            />
            <Styled.ForgotButton>Forgot ?</Styled.ForgotButton>
          </Styled.PasswordPart>
          <Styled.KeepLoggedin clickLoggedin={clickLoggedin}>
            <CheckCircleIcon onClick={keepLoggedIn} />
            <p onClick={keepLoggedIn}>Keep logged in</p>
          </Styled.KeepLoggedin>
          <Styled.LoginButton type="submit">Log in</Styled.LoginButton>
        </Styled.LoginForm>
      </Styled.RightBody>
    </Styled.Root>
  );
}
