import * as React from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import plus from "../../../assets/img/plus.svg";
import { useDispatch, useSelector } from "react-redux";
import { UploadInput } from "../../../components/pages";
import { defaultPostApiSet } from "../../../utils/api";
import { driverManagementLoading } from "../../../store/reducers/driverManagementSlice";
import { PATH, URLPATH } from "../../../assets/data/common";
import { useHistory } from "react-router-dom";
import PasswordInput from "../../../components/input/PasswordInput";
import IdInput from "../../../components/input/IdInput";
import { makeCryptoFunction } from "../../../utils/crypto";
import PortalModal from "../../../components/modal/Portal";
import { Check } from "../../../components/modal/custom";

const Styled = {
  DriverManagementRegistrationStyle: styled.div`
    padding: 0 100px;
    background-color: #fff;
    .page-label {
      font-size: 1.88rem;
    }
    .page-sub-label {
      margin-top: 16px;
      color: #ff7474;
      font-size: 0.88rem;
    }
    .dfInputMarginTop + .dfInputMarginTop {
      margin-top: 43px;
    }
    .driverInfo,
    .bankInfo,
    .createInfo {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100vh;

      & > div {
        width: 100%;
      }
    }
    .bankInfo-input-box {
      margin-top: 78px;
    }

    .createInfo-input-box {
      margin-top: 54px;
    }

    .confirm-wrap {
      display: flex;
      justify-content: flex-end;
      margin-top: 150px;

      .confirm-btn {
        width: 138px;
        height: 44px;
        line-height: 44px;
        border-radius: 5px;
        border: none;
        background-color: #5786ed;
        color: #fff;
        font-size: 0.88rem;
        text-align: center;
      }
    }
  `,
  ProfileInputBox: styled.div`
    display: flex;
    margin-top: 36px;
    margin-bottom: 53px;

    .upload-img-box {
      position: relative;
      width: 184px;
      height: 184px;
      border: 1px dotted #898989;
      background: url(${plus}) no-repeat center;

      & > input[type="file"] {
        opacity: 0;
        cursor: pointer;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
      }
      .preview {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    .profile-input-box {
      margin-left: 20px;

      .profile-input-wrap {
        display: flex;
        margin-bottom: 31px;

        & > .profile-input {
          flex: 1 1 0;
        }
      }

      .profile-input + .profile-input {
        margin-left: 10px;
      }
    }
  `,
  InputBox: styled.div`
    font-size: 0.88rem;
    color: #646464;

    & > label {
      display: block;

      &.uploader {
        width: 100%;
        margin-top: 11px;
        color: #a8a8a8;
        padding: 0 20px;
        height: 50px;
        line-height: 50px;
        border: none;
        background-color: #f7f7f7;
        cursor: pointer;
      }
    }
    & > input {
      display: block;
      width: 100%;
      margin-top: 11px;
      color: #a8a8a8;
      padding: 0 20px;
      height: 50px;
      line-height: 50px;
      border: none;
      border-radius: 5px;
      background-color: #f7f7f7;
    }
    & > input[type="file"] {
      display: none;
    }
  `,
  Agreement: styled.div`
    margin-top: 172px;

    li {
      display: flex;

      & > input {
        width: 16px;
        height: 16px;
      }
      & > label {
        margin-left: 40px;
        color: #707070;
        font-size: 0.88rem;
      }
    }
    li + li {
      margin-top: 25px;
    }
  `,
  Tracker: styled.div`
    position: fixed;
    right: 0;
    top: 0;
    background-color: #000;
  `,
};

const initState = {
  profilePreview: null,
  profileImg: null,
  driverImg: null,
  transportImg: null,
  input: {
    first_name: null,
    last_name: null,
    phone: null,
    bank_name: null,
    bank_branch: null,
    bank_account_holder: null,
    bank_account_num: null,
    id: null,
    password: null,
    checkPassword: false,
    isCheckId: false,
    isCheckPass: false,
  },
  modal: {
    visible: false,
    content: "",
  },
};

export default function DriverManagementRegistration() {
  const [state, setState] = useImmer(initState);
  const isLoading = useSelector((state) => state.driverManagement.isLoading);
  const dispatch = useDispatch();
  const history = useHistory();

  const onSubmit = async (e) => {
    e.preventDefault();
    if (
      !state.profileImg ||
      !state.transportImg ||
      !state.driverImg ||
      !state.input.first_name ||
      !state.input.last_name ||
      !state.input.phone ||
      !state.input.bank_name ||
      !state.input.bank_branch ||
      !state.input.bank_account_holder ||
      !state.input.bank_account_num ||
      !state.input.id ||
      !state.input.password ||
      !state.input.checkPassword
    ) {
      // console.log("put in data");
      modalVisible(true);
      changeModal("alert");
      return;
    }

    if (!state.input.isCheckId || !state.input.isCheckPass) {
      // console.log("check your id or password");
      modalVisible(true);
      changeModal("alert");
      return;
    }

    const form = new FormData();
    const cryptoedPassword = makeCryptoFunction(state.input.password);
    form.append("files", state.profileImg);
    form.append("files", state.driverImg);
    form.append("files", state.transportImg);
    form.append("first_name", state.input.first_name);
    form.append("last_name", state.input.last_name);
    form.append("phone", state.input.phone);
    form.append("bank_name", state.input.bank_name);
    form.append("bank_account_holder", state.input.bank_account_holder);
    form.append("bank_account_num", state.input.bank_account_num);
    form.append("bank_branch", state.input.bank_branch);
    form.append("id", state.input.id);
    form.append("password", cryptoedPassword);

    try {
      dispatch(driverManagementLoading(true));
      await defaultPostApiSet(form, URLPATH.logisDriver);
      dispatch(driverManagementLoading(false));
      history.push(PATH.driverManagement);
    } catch (e) {
      console.log("error", e);
      setState((draft) => {
        draft = initState;
      });
      dispatch(driverManagementLoading(false));
    }
  };

  const previewOnChangeFile = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    if (!file) return;
    reader.onload = () => {
      setState((draft) => {
        draft.profileImg = file;
        draft.profilePreview = reader.result;
      });
    };
    reader.readAsDataURL(file);
  };

  const onChangeFile = (key) => (file) => {
    setState((draft) => {
      draft[key] = file;
    });
  };

  const onInputChangeCompo = (key) => (value) => {
    setState((draft) => {
      draft.input[key] = value;
    });
  };

  const onInputChange = (key) => (e) => {
    const value = e.target.value;
    setState((draft) => {
      draft.input[key] = value;
    });
  };

  const getIsPassRule = (key) => (isPass) => {
    setState((draft) => {
      draft.input[key] = isPass;
    });
  };

  const checkPassword = (pass) => {
    if (state.input.password === pass) {
      setState((draft) => {
        draft.input.checkPassword = true;
      });
    } else {
      setState((draft) => {
        draft.input.checkPassword = false;
      });
    }
  };

  const modalVisible = (isOpen) => {
    setState((draft) => {
      draft.modal.visible = isOpen;
    });
  };

  const changeModal = (content) => {
    setState((draft) => {
      draft.modal.content = content;
    });
  };

  const ProfileInputBox = (
    <Styled.ProfileInputBox>
      <div className="upload-img-box">
        {state.profilePreview && (
          <img src={state.profilePreview} className="preview" />
        )}
        <input
          type="file"
          accept="image/*"
          onChange={previewOnChangeFile}
          required
          name="profile"
        />
      </div>
      <div className="profile-input-box">
        <div className="profile-input-wrap">
          <Styled.InputBox className="profile-input">
            <label htmlFor="firstName">First Name</label>
            <input
              type="text"
              placeholder="Enter First name"
              id="firstName"
              onChange={onInputChange("first_name")}
              required
            />
          </Styled.InputBox>
          <Styled.InputBox className="profile-input">
            <label htmlFor="lastName">Last Name</label>
            <input
              type="text"
              placeholder="Enter Last name"
              id="lastName"
              onChange={onInputChange("last_name")}
              required
            />
          </Styled.InputBox>
        </div>
        <Styled.InputBox className="profile-input">
          <label htmlFor="contact">Contact</label>
          <input
            type="text"
            placeholder="Enter driver cell phone Number"
            id="contact"
            onChange={onInputChange("phone")}
            required
          />
        </Styled.InputBox>
      </div>
    </Styled.ProfileInputBox>
  );

  const agreementData = [
    {
      id: "fisrtAgree",
      text:
        "I consent to Coconut Silo providing my personal information to third parties.*",
    },
    {
      id: "secondtAgree",
      text: "I agree to the collection and use of personal information .*",
    },
    {
      id: "thridAgree",
      text: `Coconut Silo will provide my personal information to Coconut Silo Service\nProvider agrees to: *`,
    },
  ];

  const agreement = (
    <Styled.Agreement>
      <ul>
        {agreementData.map((i) => {
          return (
            <li>
              <input type="checkbox" id={i.id} required />
              <label htmlFor={i.id}>
                {i.text.split("\n").map((line) => {
                  return (
                    <>
                      {line}
                      <br />
                    </>
                  );
                })}
              </label>
            </li>
          );
        })}
      </ul>
    </Styled.Agreement>
  );

  const modalCont = {
    alert: (
      <Check
        doSomethingBtnText="ok"
        title="입력 값 재확인"
        info="입력된 데이터를 확인해주세요."
        onCancel={() => modalVisible(false)}
      />
    ),
  };

  return (
    <LoadingWrap pageSlice="driverManagement">
      {/* <Styled.Tracker>tracker</Styled.Tracker> */}
      {state.modal.visible && (
        <PortalModal visible={state.modal.visible} setVisible={modalVisible}>
          {modalCont[state.modal.content]}
        </PortalModal>
      )}
      <Styled.DriverManagementRegistrationStyle>
        <div className="driverInfo">
          <div className="regisInfoInner">
            <div className="page-label">Driver Information</div>
            {ProfileInputBox}
            <Styled.InputBox className="dfInputMarginTop">
              <label>Driver License</label>
              <UploadInput
                placeHolder="Upload Driver License"
                setFile={onChangeFile("driverImg")}
                accept="image/*"
                bindId="driver"
                required
              />
            </Styled.InputBox>
            <Styled.InputBox className="dfInputMarginTop">
              <label>Transport License</label>
              <UploadInput
                placeHolder="Upload Transport License"
                setFile={onChangeFile("transportImg")}
                accept="image/*"
                bindId="transport"
                required
              />
            </Styled.InputBox>
            {agreement}
          </div>
        </div>
        <div className="bankInfo">
          <div className="regisInfoInner">
            <div className="page-label">Account Registration</div>
            <div className="page-sub-label">
              If the account information does not match, the deposit may not be
              made.
            </div>
            <div className="bankInfo-input-box">
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankName">Name of Bank</label>
                <input
                  type="text"
                  placeholder="Enter name of Bank"
                  id="bankName"
                  onChange={onInputChange("bank_name")}
                  required
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankBranch">Branch</label>
                <input
                  type="text"
                  placeholder="Enter the driver bank branch"
                  id="bankBranch"
                  onChange={onInputChange("bank_branch")}
                  required
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankAccountNum">Account Number</label>
                <input
                  type="text"
                  placeholder="Enter driver account number"
                  id="bankAccountNum"
                  onChange={onInputChange("bank_account_num")}
                  required
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankAccountHolder">Account holder</label>
                <input
                  type="text"
                  placeholder="Enter driver account holder"
                  id="bankAccountHolder"
                  onChange={onInputChange("bank_account_holder")}
                  required
                />
              </Styled.InputBox>
            </div>
          </div>
        </div>
        <div className="createInfo">
          <div className="regisInfoInner">
            <div className="page-label">Create ID</div>
            <div className="createInfo-input-box">
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="id">Driver ID</label>
                <IdInput
                  placeholder="Enter the ID to be given to the driver"
                  id="id"
                  onChangeInput={onInputChangeCompo("id")}
                  required
                  alertMsg="The ID that already exists"
                  alertMsgId="Only 4-16 lowercase letters + numbers are allowed"
                  getIsPassRule={getIsPassRule("isCheckId")}
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="password">Password</label>
                <PasswordInput
                  placeholder="Enter password"
                  id="password"
                  onChangeInput={onInputChangeCompo("password")}
                  required
                  alertMsg="Use at least 8 lowercase letter + numbers"
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="checkPassword">Confirm Password</label>
                <PasswordInput
                  placeholder="Enter one more time"
                  id="checkPassword"
                  onChangeInput={onInputChangeCompo("checkPassword")}
                  required
                  alertMsg="Password isn't the same"
                  compareModeValue={state.input.password}
                  getIsPassRule={getIsPassRule("isCheckPass")}
                />
              </Styled.InputBox>
            </div>
            <div className="confirm-wrap">
              <button
                className="confirm-btn"
                disabled={isLoading}
                // style={{backgroundColor: "#aac2f6"}}
                onClick={onSubmit}
              >
                Confirm
              </button>
            </div>
          </div>
        </div>
      </Styled.DriverManagementRegistrationStyle>
    </LoadingWrap>
  );
}
