import { useImmer } from "use-immer";
import styled from "styled-components";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import plus from "../../../assets/img/plus.svg";
import { useDispatch, useSelector } from "react-redux";
import { UploadInput } from "../../../components/pages";
import { defaultGetApiSet, defaultPutApiSet } from "../../../utils/api";
import { driverManagementLoading } from "../../../store/reducers/driverManagementSlice";
import { PATH, URLPATH } from "../../../assets/data/common";
import { useHistory } from "react-router-dom";
import { s3ConverData } from "../../../utils/s3Convert";

const Styled = {
  DriverManagementModifyStyle: styled.form`
    padding: 0 100px;
    background-color: #fff;
    .page-label {
      font-size: 1.88rem;
    }
    .page-sub-label {
      margin-top: 16px;
      color: #ff7474;
      font-size: 0.88rem;
    }
    .dfInputMarginTop + .dfInputMarginTop {
      margin-top: 43px;
    }
    .driverInfo,
    .bankInfo,
    .createInfo {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100vh;

      & > div {
        width: 100%;
      }
    }
    .bankInfo-input-box {
      margin-top: 78px;
    }

    .createInfo-input-box {
      margin-top: 54px;
    }

    .confirm-wrap {
      display: flex;
      justify-content: flex-end;
      margin-top: 150px;

      .confirm-btn {
        width: 138px;
        height: 44px;
        line-height: 44px;
        border-radius: 5px;
        border: none;
        background-color: #5786ed;
        color: #fff;
        font-size: 0.88rem;
        text-align: center;
      }
    }
  `,
  ProfileInputBox: styled.div`
    display: flex;
    margin-top: 36px;
    margin-bottom: 53px;

    .upload-img-box {
      position: relative;
      width: 184px;
      height: 184px;
      border: 1px dotted #898989;
      background: url(${plus}) no-repeat center;

      & > input[type="file"] {
        opacity: 0;
        cursor: pointer;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
      }
      .preview {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    .profile-input-box {
      margin-left: 20px;

      .profile-input-wrap {
        display: flex;
        margin-bottom: 31px;

        & > .profile-input {
          flex: 1 1 0;
        }
      }

      .profile-input + .profile-input {
        margin-left: 10px;
      }
    }
  `,
  InputBox: styled.div`
    font-size: 0.88rem;
    color: #646464;

    & > label {
      display: block;

      &.uploader {
        width: 100%;
        margin-top: 11px;
        color: #a8a8a8;
        padding: 0 20px;
        height: 50px;
        line-height: 50px;
        border: none;
        background-color: #f7f7f7;
        cursor: pointer;
      }
    }
    & > input {
      display: block;
      width: 100%;
      margin-top: 11px;
      color: #a8a8a8;
      padding: 0 20px;
      height: 50px;
      line-height: 50px;
      border: none;
      border-radius: 5px;
      background-color: #f7f7f7;
    }
    & > input[type="file"] {
      display: none;
    }
  `,
  Agreement: styled.div`
    margin-top: 172px;

    li {
      display: flex;

      & > input {
        width: 16px;
        height: 16px;
      }
      & > label {
        margin-left: 40px;
        color: #707070;
        font-size: 0.88rem;
      }
    }
    li + li {
      margin-top: 25px;
    }
  `,
  Tracker: styled.div`
    position: fixed;
    right: 0;
    top: 0;
    background-color: #000;
  `,
};

const initState = {
  profilePreview: null,
  profileImg: null,
  prevProfileImg: null,
  driverImg: null,
  prevDriverImg: null,
  transportImg: null,
  prevTransportImg: null,
  input: {
    first_name: null,
    last_name: null,
    phone: null,
    bank_name: null,
    bank_branch: null,
    bank_account_holder: null,
    bank_account_num: null,
  },
  deleteString: "",
};

export default function DriverManagementModify() {
  const [state, setState] = useImmer(initState);
  const isLoading = useSelector((state) => state.driverManagement.isLoading);
  const modifyTarget = useSelector(
    (state) => state.driverManagement.modifyTarget
  );
  const dispatch = useDispatch();
  const history = useHistory();
  const setDeleteFileString = (data) => {
    let deleteString = "";
    if (deleteString.length) {
      setState((draft) => {
        draft.deleteString.concat(",", data);
      });
    } else {
      setState((draft) => {
        draft.deleteString.concat("", data);
      });
    }
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    let deleteString = "";
    const form = new FormData();
    // 초기 데이터 받은것과 변경됨 비교 후 이미지 추가
    if (state.profileImg !== state.prevProfileImg) {
      // console.log("profile", deleteString);
      form.append("files", state.profileImg);
      deleteString = state.prevProfileImg;
    }

    if (state.driverImg !== state.prevDriverImg) {
      form.append("files", state.driverImg);
      if (deleteString.length) {
        deleteString = deleteString + "," + state.prevDriverImg;
      } else {
        deleteString = state.prevDriverImg;
      }
    }

    if (state.transportImg !== state.prevTransportImg) {
      form.append("files", state.transportImg);
      if (deleteString.length) {
        deleteString = deleteString + "," + state.prevTransportImg;
      } else {
        deleteString = state.prevTransportImg;
      }
    }

    deleteString.length && form.append("delete_files", deleteString);
    form.append("first_name", state.input.first_name);
    form.append("last_name", state.input.last_name);
    form.append("phone", state.input.phone);
    form.append("bank_name", state.input.bank_name);
    form.append("bank_account_holder", state.input.bank_account_holder);
    form.append("bank_account_num", state.input.bank_account_num);
    form.append("bank_branch", state.input.bank_branch);

    try {
      dispatch(driverManagementLoading(true));
      await defaultPutApiSet(form, `${URLPATH.logisDriver}/${modifyTarget}`);
      dispatch(driverManagementLoading(false));
      history.push(PATH.driverManagement);
    } catch (e) {
      console.log("error", e);
      setState((draft) => {
        draft = initState;
      });
      dispatch(driverManagementLoading(false));
    }
  };

  const previewOnChangeFile = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    if (!file) return;
    reader.onload = () => {
      setState((draft) => {
        draft.profileImg = file;
        draft.profilePreview = reader.result;
      });
    };
    reader.readAsDataURL(file);
  };

  const onChangeFile = (key) => (file) => {
    setState((draft) => {
      draft[key] = file;
    });
  };

  const onInputChange = (key) => (e) => {
    const value = e.target.value;

    setState((draft) => {
      draft.input[key] = value;
    });
  };

  const setData = async () => {
    try {
      dispatch(driverManagementLoading(true));
      const res = await defaultGetApiSet(
        "",
        `${URLPATH.logisDriver}/${modifyTarget}`
      );
      res.data.profile &&
        s3ConverData(res.data.s3_folder, res.data.profile).then((img) => {
          setState((draft) => {
            draft.profilePreview = img;
          });
        });

      setState((draft) => {
        draft.prevProfileImg = res.data.profile;
        draft.profileImg = res.data.profile;
        draft.prevDriverImg = res.data.driver_license;
        draft.driverImg = res.data.driver_license;
        draft.prevTransportImg = res.data.logis_license;
        draft.transportImg = res.data.logis_license;
        draft.input.first_name = res.data.first_name;
        draft.input.last_name = res.data.last_name;
        draft.input.phone = res.data.phone;
        draft.input.bank_account_holder = res.data.bank_account_holder;
        draft.input.bank_account_num = res.data.bank_account_num;
        draft.input.bank_branch = res.data.bank_branch;
        draft.input.bank_name = res.data.bank_name;
      });
      dispatch(driverManagementLoading(false));
    } catch (e) {}
  };

  React.useEffect(() => {
    if (!modifyTarget) {
      history.push(PATH.driverManagement);
    }
    setData();
  }, []);

  const ProfileInputBox = (
    <Styled.ProfileInputBox>
      <div className="upload-img-box">
        {state.profilePreview && (
          <img src={state.profilePreview} className="preview" />
        )}
        <input
          type="file"
          accept="image/*"
          onChange={previewOnChangeFile}
          name="profile"
        />
      </div>
      <div className="profile-input-box">
        <div className="profile-input-wrap">
          <Styled.InputBox className="profile-input">
            <label htmlFor="firstName">First Name</label>
            <input
              type="text"
              placeholder="Enter First name"
              id="firstName"
              onChange={onInputChange("first_name")}
              value={state.input.first_name}
            />
          </Styled.InputBox>
          <Styled.InputBox className="profile-input">
            <label htmlFor="lastName">Last Name</label>
            <input
              type="text"
              placeholder="Enter Last name"
              id="lastName"
              onChange={onInputChange("last_name")}
              value={state.input.last_name}
            />
          </Styled.InputBox>
        </div>
        <Styled.InputBox className="profile-input">
          <label htmlFor="contact">Contact</label>
          <input
            type="text"
            placeholder="Enter driver cell phone Number"
            id="contact"
            onChange={onInputChange("phone")}
            value={state.input.phone}
          />
        </Styled.InputBox>
      </div>
    </Styled.ProfileInputBox>
  );

  const agreementData = [
    {
      id: "fisrtAgree",
      text:
        "I consent to Coconut Silo providing my personal information to third parties.*",
    },
    {
      id: "secondtAgree",
      text: "I agree to the collection and use of personal information .*",
    },
    {
      id: "thridAgree",
      text: `Coconut Silo will provide my personal information to Coconut Silo Service\nProvider agrees to: *`,
    },
  ];

  const agreement = (
    <Styled.Agreement>
      <ul>
        {agreementData.map((i) => {
          return (
            <li>
              <input type="checkbox" id={i.id} required checked />
              <label htmlFor={i.id}>
                {i.text.split("\n").map((line) => {
                  return (
                    <>
                      {line}
                      <br />
                    </>
                  );
                })}
              </label>
            </li>
          );
        })}
      </ul>
    </Styled.Agreement>
  );

  return (
    <LoadingWrap pageSlice="driverManagement">
      {/* <Styled.Tracker>tracker</Styled.Tracker> */}
      <Styled.DriverManagementModifyStyle onSubmit={onSubmit}>
        <div className="driverInfo">
          <div className="regisInfoInner">
            <div className="page-label">Driver Information</div>
            {ProfileInputBox}
            <Styled.InputBox className="dfInputMarginTop">
              <label>Driver License</label>
              <UploadInput
                placeHolder={
                  state.prevDriverImg
                    ? "Your Driver License"
                    : "Upload Driver License"
                }
                setFile={onChangeFile("driverImg")}
                accept="image/*"
                bindId="driver"
              />
            </Styled.InputBox>
            <Styled.InputBox className="dfInputMarginTop">
              <label>Transport License</label>
              <UploadInput
                placeHolder={
                  state.prevTransportImg
                    ? "Your Transport License"
                    : "Upload Transport License"
                }
                setFile={onChangeFile("transportImg")}
                accept="image/*"
                bindId="transport"
              />
            </Styled.InputBox>
          </div>
        </div>
        <div className="bankInfo">
          <div className="regisInfoInner">
            <div className="page-label">Account Registration</div>
            <div className="page-sub-label">
              If the account information does not match, the deposit may not be
              made.
            </div>
            <div className="bankInfo-input-box">
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankName">Name of Bank</label>
                <input
                  type="text"
                  placeholder="Enter name of Bank"
                  id="bankName"
                  onChange={onInputChange("bank_name")}
                  value={state.input.bank_name}
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankBranch">Branch</label>
                <input
                  type="text"
                  placeholder="Enter the driver bank branch"
                  id="bankBranch"
                  onChange={onInputChange("bank_branch")}
                  value={state.input.bank_branch}
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankAccountNum">Account Number</label>
                <input
                  type="text"
                  placeholder="Enter driver account number"
                  id="bankAccountNum"
                  onChange={onInputChange("bank_account_num")}
                  value={state.input.bank_account_num}
                />
              </Styled.InputBox>
              <Styled.InputBox className="dfInputMarginTop">
                <label htmlFor="bankAccountHolder">Account holder</label>
                <input
                  type="text"
                  placeholder="Enter driver account holder"
                  id="bankAccountHolder"
                  onChange={onInputChange("bank_account_holder")}
                  value={state.input.bank_account_holder}
                />
              </Styled.InputBox>
            </div>
            <div className="confirm-wrap">
              <button
                className="confirm-btn"
                type="submit"
                disabled={isLoading}
                // style={{backgroundColor: "#aac2f6"}}
              >
                Confirm
              </button>
            </div>
          </div>
        </div>
      </Styled.DriverManagementModifyStyle>
    </LoadingWrap>
  );
}
