// asset
import edit from "../../../assets/img/edit.svg";
import plus from "../../../assets/img/plus.svg";
import plus_blue from "../../../assets/img/plus_blue.svg";
import plus_white from "../../../assets/img/plus_white.svg";
import edit_submit from "../../../assets/img/edit_submit.svg";
import email from "../../../assets/img/email.svg";
import person from "../../../assets/img/person.svg";
import phone from "../../../assets/img/phone.svg";
import lock from "../../../assets/img/lock.svg";
import icon_delete from "../../../assets/img/delete.svg";
import plus_disable from "../../../assets/img/plus_disable.svg";

import * as React from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import PageLabel from "../../../components/PageLabel";
import LabelInput from "../../../components/input/LabelInput";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import { useDispatch, useSelector } from "react-redux";
import { settingLoading } from "../../../store/reducers/settingSlice";
import {
  calcTotalPage,
  transformCheckTableId,
} from "../../../utils/convertData";
import CheckTable from "../../../components/table/CheckTable";
import { memberListCol } from "../../../assets/data/setting/tableData";
import Pagenation from "../../../components/pagination";
import {
  defaultDelApiSet,
  defaultGetApiSet,
  defaultPostApiSet,
  defaultPutApiSet,
} from "../../../utils/api";
import { URLPATH } from "../../../assets/data/common";
import PortalModal from "../../../components/modal/Portal";
import { Check, Confirm } from "../../../components/modal/custom";
import CreateId from "../../../components/modal/custom/CreateID";
import { jwtVerify } from "../../../utils/jwt";
import { s3ConverData } from "../../../utils/s3Convert";
import ChangePassword from "../../../components/modal/custom/ChangePassword";
import { BgLayout } from "../../../components/layout";
import { makeCryptoFunction } from "../../../utils/crypto";

const Styled = {
  Setting: styled.div`
    padding: 96px 0 53px;
    min-height: calc(100vh - 50px);
    max-width: 1420px;
    margin: 0 auto;
    background-color: #f7f7f7;

    .setting-box {
      display: flex;
      margin-top: 31px;
    }

    .header-label {
      background-color: #888888;
      color: #fff;
      display: flex;
      justify-content: space-between;
      padding: 0 40px;
      align-items: center;
      height: 60px;

      & > h3 {
        font-family: sans-serif;
        font-size: 1.25rem;
      }

      & button.btn {
        border: none;
        text-indent: -99999px;
        width: 24px;
        height: 24px;
      }
    }
  `,
  AccountBox: styled.div`
    width: 465px;
    border-radius: 5px;
    background-color: #fff;
    overflow: hidden;
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.16);

    input:disabled {
      background-color: transparent;
    }

    .header-label {
      & > button.edit-btn {
        background: url(${edit}) no-repeat center;
      }

      & > button.submit-btn {
        background: url(${edit_submit}) no-repeat center;
      }
    }

    .profile-box {
      padding: 40px 0 49px;
      border-bottom: 1px solid #e6e6e6;
      .profile {
        display: flex;
        flex-direction: column;
        align-items: center;

        .grade {
          font-size: 0.75rem;
          margin-top: 33px;
          color: #5786ed;
        }
        .name {
          margin-top: 8px;
          font-size: 1.25rem;
          color: #939393;
          border: 1px solid transparent;
          outline: none;
          text-align: center;
        }

        .upload-img-box {
          position: relative;
          width: 120px;
          height: 120px;
          border: 1px dotted #939393;
          border-radius: 50%;
          overflow: hidden;

          & > input[type=file] {
            opacity: 0;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
          }
          .preview {
            display: block;
            width: 100%;
            height: 100%;
          }
        }
      }
      .profile-info {
        margin-top: 40px;
        padding: 0 40px;

        .info-row {
          display: flex;
          justify-content: space-between;
        }

        .info-row + .info-row {
          margin-top: 14px;
        }
      }
    }

    .company-box {
      .company-info {
        display: flex;
        align-items: center;
        padding: 15px 40px;
        border-bottom: 1px solid #e6e6e6;
        &:last-child {
          border: none;
        }
      }
    }

    /* edit or checked mode  start */
    &.edit .header-label {
        background-color: #aac2f6;
      }
    }
    &.edit {
      .profile-box {
        .profile {
          .grade {
            color: #6d7581;
          }
          .name {
            color: #5786ed;
            border-bottom: 1px solid #5786ed;
          }

          .upload-img-box {
            border: 1px dotted #5786ed;
            background: url(${plus_blue}) no-repeat center;

            & > input[type=file] {
                cursor: pointer;
            }
          }
        }
      }
    }
    /* edit mode end */
  `,

  LabelInput: styled(LabelInput)`
    width: 100%;
    flex-direction: column;
    align-items: flex-start !important;

    & > label {
      margin-right: 0;
      width: 100%;
    }
    & > input {
      margin-top: 8px;
      width: 100%;
    }
  `,
  MemberBox: styled.div`
    position: relative;
    padding-bottom: 88px;
    width: 935px;
    border-radius: 5px;
    background-color: #fff;
    overflow: hidden;
    margin-left: 20px;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.16);

    input:disabled {
      background-color: transparent;
    }

    .header-label {
      .btn-box {
        .delete-btn {
          margin-right: 20px;
          background: url(${icon_delete}) no-repeat center;
        }
        .add-btn {
          background: url(${plus_white}) no-repeat center;
        }
      }
    }

    .search-result {
      height: 54px;
      line-height: 54px;
      padding: 0 50px;
      color: #b5b5b5;
      font-size: 0.88rem;
    }

    /* checked */
    &.checked {
      .header-label {
        background-color: #aac2f6;
        .add-btn {
          background: url(${plus_disable}) no-repeat center;
        }
      }
    }
    /* checked end */
  `,
  PagenationStyle: styled(Pagenation)`
    position: absolute;
    bottom: 56px;
    right: 50px;
    justify-content: flex-end;
  `,
  LabelInputStyle: styled(LabelInput)`
    & * {
      cursor: ${(props) => (props.editMode ? "pointer" : "default")};
    }
    & > input {
      border-bottom: ${(props) =>
        props.editMode ? "1px solid #5786ed" : "1px solid transparent"};
      }
    }
  `,
};

const initState = {
  editMode: false,
  checkTableList: [],
  pageData: {
    members: [],
    pageInfo: {
      totalData: null,
      currentPage: null,
      dataPerPage: null,
      totalPages: null,
      startRow: null,
    },
  },
  inputValue: {
    profileImg: null,
    firstName: "",
    lastName: "",
    email: "",
    id: "",
    contact: "",
    password: "",
    company: "",
    address: "",
    companyRegistration: "",
    taxCode: "",
    representativeNumber: "",
    representativeEmail: "",
  },
  profilePreview: null,
  modal: {
    visible: false,
    content: "",
  },
};

export default function Setting() {
  const [state, setState] = useImmer(initState);
  const dispatch = useDispatch();
  const isLoading = useSelector((state) => state.setting.isLoading);
  const loginData = jwtVerify(
    localStorage.getItem("userData")
      ? localStorage.getItem("userData")
      : sessionStorage.getItem("userData")
  );

  const isManager = !!loginData.is_manager;

  const onChangeMode = () => {
    setState((draft) => {
      draft.editMode = true;
    });
  };
  const changeInfo = async () => {
    try {
      // 유저 정보 변경
      if (
        (state && !state.inputValue.firstName) ||
        !state.inputValue.lastName ||
        !state.inputValue.email ||
        !state.inputValue.contact
      ) {
        visibleModal(true);
        changeModal("alert");
      }
      const form = new FormData();
      const firstName = state.inputValue.firstName;
      const lastName = state.inputValue.lastName;
      form.append("email", state.inputValue.email);
      form.append("phone", state.inputValue.contact);
      form.append("first_name", firstName);
      form.append("last_name", lastName);
      state.inputValue.profileImg &&
        form.append("files", state.inputValue.profileImg);
      //
      // 회사 정보 변경
      const formSecond = new FormData();
      formSecond.append("phone", state.inputValue.representativeNumber);
      state.inputValue.representativeEmail &&
        formSecond.append("email", state.inputValue.representativeEmail);
      //
      dispatch(settingLoading(true));
      await defaultPutApiSet(form, URLPATH.loogisMember);
      await defaultPutApiSet(formSecond, URLPATH.logis);
      setState((draft) => {
        draft.editMode = false;
      });
      dispatch(settingLoading(false));
    } catch (e) {
      dispatch(settingLoading(false));
    }
  };

  const setInputValue = (key) => (value) => {
    setState((draft) => {
      draft.inputValue[key] = value;
    });
  };

  const previewOnChangeFile = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    if (!file) return;
    reader.onload = () => {
      setState((draft) => {
        draft.inputValue.profileImg = file;
        draft.profilePreview = reader.result;
      });
    };
    reader.readAsDataURL(file);
  };
  const visibleModal = (visible) => {
    setState((draft) => {
      draft.modal.visible = visible;
    });
  };

  const changeModal = (content) => {
    setState((draft) => {
      draft.modal.content = content;
    });
  };

  const addMember = () => {
    visibleModal(true);
    changeModal("addMember");
  };

  const deletePopover = () => {
    visibleModal(true);
    changeModal("confirm");
  };

  const modalCancel = () => {
    visibleModal(false);
  };

  const onChangeName = (key) => (e) => {
    const value = e.target.value;
    setState((draft) => {
      draft.inputValue[key] = value;
    });
  };

  const deleteMember = async () => {
    try {
      dispatch(settingLoading(true));
      const req = state.checkTableList;
      const params = {
        count: 10,
      };
      await defaultDelApiSet(req, URLPATH.loogisMember);
      await memberListApi(params);
      visibleModal(false);
      dispatch(settingLoading(false));
    } catch (e) {
      visibleModal(false);
      dispatch(settingLoading(false));
    }
  };

  const createId = async (state) => {
    if (
      state &&
      (!state.id ||
        !state.password ||
        !state.checkPassword ||
        !state.isCheckPass)
    ) {
      visibleModal(true);
      changeModal("alert");
    } else {
      try {
        const req = {
          count: 10,
        };
        const form = new FormData();
        form.append("email", state.id);
        form.append("password", makeCryptoFunction(state.password));

        dispatch(settingLoading(true));
        await defaultPostApiSet(form, URLPATH.loogisMember);
        await memberListApi(req);

        visibleModal(false);
        dispatch(settingLoading(false));
      } catch (e) {
        visibleModal(false);
        dispatch(settingLoading(false));
      }
    }
  };

  const getCheckList = (value) => {
    setState((draft) => {
      draft.checkTableList = value;
    });
  };

  const memberListApi = async (req) => {
    try {
      dispatch(settingLoading(true));
      const res = await defaultGetApiSet(req, URLPATH.loogisMember);
      setState((draft) => {
        draft.pageData = transformCheckTableId(
          res.data,
          "members",
          "member_id"
        );
      });
      dispatch(settingLoading(false));
    } catch (e) {
      dispatch(settingLoading(false));
    }
  };

  const clickPagination = async (page) => {
    try {
      dispatch(settingLoading(true));
      const req = {
        count: 10,
        page: page,
      };
      await memberListApi(req);
      dispatch(settingLoading(false));
    } catch (e) {
      dispatch(settingLoading(false));
    }
  };

  const modalChangePassword = () => {
    visibleModal(true);
    changeModal("changePassword");
  };

  const changePassword = async (input) => {
    try {
      dispatch(settingLoading(true));
      const form = new FormData();
      form.append("password", makeCryptoFunction(input.oldPassword));
      form.append("new_password", makeCryptoFunction(input.checkPassword));
      await defaultPutApiSet(form, `${URLPATH.loogisMember}/password`);
      dispatch(settingLoading(false));
    } catch (e) {
      dispatch(settingLoading(false));
    }
  };

  const getProfileApi = async () => {
    try {
      dispatch(settingLoading(true));
      const res = await defaultGetApiSet(
        "",
        `${URLPATH.loogisMember}/${loginData.userid}`
      );
      const profileImg = await s3ConverData(
        res.data?.s3_folder,
        res.data?.file_id
      );

      setState((draft) => {
        draft.profilePreview = profileImg;
        draft.inputValue.firstName = res.data?.first_name;
        draft.inputValue.lastName = res.data?.last_name;
        draft.inputValue.email = res.data?.email;
        draft.inputValue.id = res.data?.email;
        draft.inputValue.contact = res.data?.phone;
      });
      dispatch(settingLoading(false));
    } catch (e) {
      dispatch(settingLoading(false));
    }
  };

  const getLogisInfoApi = async () => {
    try {
      dispatch(settingLoading(true));
      const res = await defaultGetApiSet("", URLPATH.logis);

      setState((draft) => {
        draft.inputValue.company = res.data.company_name;
        draft.inputValue.address = res.data.addr;
        draft.inputValue.companyRegistration = res.data.license;
        draft.inputValue.taxCode = res.data.tax_code;
        draft.inputValue.representativeNumber = res.data.phone;
        draft.inputValue.representativeEmail = res.data.email;
      });
      dispatch(settingLoading(false));
    } catch (e) {
      dispatch(settingLoading(false));
    }
  };

  React.useEffect(() => {
    if (state.editMode) return;
    getProfileApi();
    getLogisInfoApi();
  }, [state.editMode]);

  React.useEffect(() => {
    const req = {
      count: 10,
    };
    memberListApi(req);
  }, []);

  const totalPage = calcTotalPage(
    state.pageData.pageInfo.totalData,
    state.pageData.pageInfo.dataPerPage
  );

  const modalCont = {
    confirm: (
      <Confirm
        title="You are about to delete a member?"
        info="This will delete your member from MEMBER LIST.\nAre you sure?"
        doSomethingBtnText="Delete"
        doSomethingColor="#FF7474"
        doSomething={deleteMember}
        onCancel={modalCancel}
      />
    ),
    addMember: (
      <CreateId
        onSubmit={createId}
        onCancel={modalCancel}
        apiUrl={`${URLPATH.loogisMember}/exist`}
      />
    ),
    alert: (
      <Check
        doSomethingBtnText="ok"
        title="입력 값 재확인"
        info="입력된 데이터를 확인해주세요."
        onCancel={modalCancel}
      />
    ),
    changePassword: (
      <ChangePassword onSubmit={changePassword} onCancel={modalCancel} />
    ),
  };

  return (
    <LoadingWrap pageSlice="setting">
      {state.modal.visible && (
        <PortalModal visible={state.modal.visible} setVisible={visibleModal}>
          {modalCont[state.modal.content]}
        </PortalModal>
      )}
      <BgLayout bgColor="#F7F7F7">
        <Styled.Setting>
          <PageLabel imgType={"icon_setting"} labelText="Setting" />
          <div className="setting-box">
            <Styled.AccountBox className={state.editMode ? "edit" : null}>
              <div className="header-label">
                <h3>Account</h3>
                {state.editMode ? (
                  <button className="btn submit-btn" onClick={changeInfo}>
                    submit
                  </button>
                ) : (
                  <button className="btn edit-btn" onClick={onChangeMode}>
                    edit
                  </button>
                )}
              </div>
              <div className="profile-box">
                <div className="profile">
                  <div className="upload-img-box">
                    {state.profilePreview && (
                      <img src={state.profilePreview} className="preview" />
                    )}
                    <input
                      type="file"
                      accept="image/*"
                      onChange={previewOnChangeFile}
                      name="profile"
                      disabled={!state.editMode}
                    />
                  </div>
                  <div className="grade">
                    Grade: {isManager ? "Manager" : "Member"}
                  </div>
                  <input
                    className="name"
                    placeholder="Enter your First Name"
                    onChange={onChangeName("firstName")}
                    disabled={!state.editMode}
                    value={state.inputValue.firstName}
                  />
                  <input
                    className="name"
                    placeholder="Enter your Last Name"
                    onChange={onChangeName("lastName")}
                    disabled={!state.editMode}
                    value={state.inputValue.lastName}
                  />
                </div>
                <div className="profile-info">
                  <div className="info-row">
                    <LabelInput
                      id="email"
                      placeholder="Enter E-mail"
                      setValue={setInputValue("email")}
                      disabled={!state.editMode}
                      value={state.inputValue.email}
                      isEdit={state.editMode}
                      labelEle={
                        <img
                          src={email}
                          style={{
                            display: "block",
                            width: "20px",
                            height: "20px",
                          }}
                        />
                      }
                    />
                    <LabelInput
                      placeholder={state.inputValue.id}
                      disabled={true}
                      value={state.inputValue.id}
                      labelEle={
                        <img
                          src={person}
                          style={{
                            display: "block",
                            width: "20px",
                            height: "20px",
                          }}
                        />
                      }
                    />
                  </div>
                  <div className="info-row">
                    <LabelInput
                      id="contact"
                      placeholder="Enter Contact"
                      disabled={!state.editMode}
                      value={state.inputValue.contact}
                      setValue={setInputValue("contact")}
                      isEdit={state.editMode}
                      labelEle={
                        <img
                          src={phone}
                          style={{
                            display: "block",
                            width: "20px",
                            height: "20px",
                          }}
                        />
                      }
                    />
                    <Styled.LabelInputStyle
                      type="password"
                      id="password"
                      value="secret"
                      disabled={true}
                      onClick={state.editMode ? modalChangePassword : null}
                      labelEle={
                        <img
                          src={lock}
                          style={{
                            display: "block",
                            width: "20px",
                            height: "20px",
                          }}
                        />
                      }
                      editMode={state.editMode}
                    />
                  </div>
                </div>
              </div>
              <div className="company-box">
                <div className="company-info">
                  <Styled.LabelInput
                    labelEle="Company"
                    id="company"
                    setValue={setInputValue("company")}
                    disabled={true}
                    value={state.inputValue.company}
                  />
                </div>
                <div className="company-info">
                  <Styled.LabelInput
                    labelEle="Address"
                    id="address"
                    setValue={setInputValue("address")}
                    disabled={true}
                    value={state.inputValue.address}
                  />
                </div>
                <div className="company-info">
                  <Styled.LabelInput
                    labelEle="Company Registration Number"
                    id="companyRegistration"
                    setValue={setInputValue("companyRegistration")}
                    disabled={true}
                    value={state.inputValue.companyRegistration}
                  />
                </div>
                <div className="company-info">
                  <Styled.LabelInput
                    labelEle="Tax Code"
                    id="taxCode"
                    setValue={setInputValue("taxCode")}
                    disabled={true}
                    value={state.inputValue.taxCode}
                  />
                </div>

                <div className="company-info">
                  <Styled.LabelInput
                    labelEle="Business representative number"
                    id="representativeNumber"
                    setValue={setInputValue("representativeNumber")}
                    disabled={!state.editMode && isManager}
                    isEdit={state.editMode && isManager}
                    value={state.inputValue.representativeNumber}
                  />
                </div>
                <div className="company-info">
                  <Styled.LabelInput
                    labelEle="Representative email address"
                    id="representativeEmail"
                    setValue={setInputValue("representativeEmail")}
                    disabled={!state.editMode && isManager}
                    isEdit={state.editMode && isManager}
                    value={state.inputValue.representativeEmail}
                  />
                </div>
              </div>
            </Styled.AccountBox>
            <Styled.MemberBox
              className={state.checkTableList.length ? "checked" : null}
            >
              <div className="header-label">
                <h3>Member List</h3>
                <div className="btn-box">
                  {state.checkTableList.length ? (
                    <>
                      <button
                        className="btn delete-btn"
                        onClick={deletePopover}
                      >
                        delete
                      </button>
                      <button className="btn add-btn" disabled>
                        add
                      </button>
                    </>
                  ) : (
                    <button className="btn add-btn" onClick={addMember}>
                      add
                    </button>
                  )}
                </div>
              </div>
              <div className="search-result">
                Showing{" "}
                {state.pageData.pageInfo.currentPage
                  ? state.pageData.pageInfo.currentPage
                  : "0"}{" "}
                page of {totalPage ? totalPage : "0"} result
              </div>
              <CheckTable
                columns={memberListCol}
                data={state.pageData.members}
                getCheckList={getCheckList}
                headAlign="left"
                bodyAlign="left"
              />
              <Styled.PagenationStyle
                currentPage={state.pageData.pageInfo.currentPage}
                totalData={state.pageData.pageInfo.totalData}
                cb={clickPagination}
              />
            </Styled.MemberBox>
          </div>
        </Styled.Setting>
      </BgLayout>
    </LoadingWrap>
  );
}
