import styled from "styled-components";
import { useImmer } from "use-immer";
import { useSelector, useDispatch } from "react-redux";
import { colors } from "../../../style/color";
import SelectForm from "../../../components/ListForm/SelectForm";
import Loading from "../../../components/loading";
import SelectBox from "../../../components/SelectBox";
import { Select } from "../../../components/search";
import {
  findingTruck,
  findingDriver,
} from "../../../assets/data/orderManagement/tableData";
import { defaultGetApiSet, defaultPostApiSet } from "../../../utils/api";
import { useHistory } from "react-router-dom";
import { URLPATH } from "../../../assets/data/common";
import { PATH } from "../../../assets/data/common";
import selectArrowWhite from "../../../assets/img/selectArrowWhite.svg";
import selectArrowGray from "../../../assets/img/selectArrowGray.svg";
import resetIcon from "../../../assets/img/resetIcon.svg";
import {
  truckListReducer,
  drvierListReducer,
  truckTypesReducer,
  truckOptionsReducer,
  garageListReducer,
} from "../../../hooks/useReducer";
import moment from "moment";
const Styled = {
  Root: styled.div``,
  OrderInfoWrapper: styled.div`
    background-color: ${colors.navyBlue};
    padding: 80px 100px 40px 100px;
    .header {
      display: flex;
      align-items: center;
      column-gap: 10px;
      margin-bottom: 40px;
    }
  `,
  OrderInfo: styled.div`
    display: grid;
    grid-template-columns: 2.53fr 1.53fr 1.53fr 2.21fr 1.85fr 1fr;
    column-gap: 20px;
  `,
  Badge: styled.div`
    font-size: 0.75rem;
    font-weight: bold;
    color: white;
    background-color: #5786ed;
    border-radius: 30px;
    width: 114px;
    line-height: 28px;
    height: 100%;
    text-align: center;
  `,
  Text: styled.p`
    color: ${(props) => props.color};
    font-size: ${(props) => props.fontsize};
    margin-bottom: ${(props) => props.marginbottom};
    font-weight: ${(props) => props.fontweight};

    display: ${(props) => (props.display === 0 ? "none" : null)};
  `,
  InfoBox: styled.div`
    color: ${colors.white};
    background-color: ${colors.lightNavyBlue};
    font-size: 0.875rem;
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 0 20px;
  `,
  StickyPart: styled.div`
    position: sticky;
    top: 0px;
    z-index: 10;
    background-color: white;
    border-bottom: 1px solid #e3e3e3;
  `,
  GroupSelectWrapper: styled.div`
    padding: 70px 100px 46px 100px;
    display: flex;
    column-gap: 40px;
  `,

  GroupContent: styled.div`
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
  `,
  SelectBoxWrapper: styled.div`
    .top {
      display: flex;
      column-gap: 5px;
      margin-bottom: 6px;
    }
    .bottom {
    }
  `,
  ConfirmButton: styled.div`
    display: flex;
    justify-content: flex-end;

    input {
      margin: 60px 100px 50px 0;
      background-color: ${(props) =>
        props.isPossible ? "#004cf2" : "#B5B5B5"};
      color: white;
      width: 150px;
      height: 50px;
      border: none;
      border-radius: 5px;
      font-size: 1rem;
      font-weight: bold;
      :hover {
        cursor: ${(props) => (props.isPossible ? "pointer" : null)};
      }
    }
  `,
  Select: styled(Select)`
    width: 100%;
    .select {
      border: ${(props) => (props.selectGarage ? null : "1px dotted")};
      border-radius: 5px;
      color: ${(props) => (props.selectGarage ? "white" : "#939393")};
      background-color: ${(props) => (props.selectGarage ? "#6D7581" : null)};
      font-weight: ${(props) => (props.selectGarage ? "bold" : null)};
      .option {
        color: black;
        font-weight: normal;
      }
      &:after {
        width: 25px;
        height: 25px;
        background: ${(props) =>
          props.selectGarage
            ? `url(${selectArrowWhite}) no-repeat center`
            : `url(${selectArrowGray}) no-repeat center`};
        background-size: cover;
      }
    }
  `,
  RestIcon: styled.img`
    :hover {
      text-decoration: underline;
      cursor: pointer;
    }
  `,
};

const initState = {
  isToggleWidth: false,
  loading: false,
  getData: {},
  costInput: 0,
  openMenu: false,
  modifyMode: false,
  count: {
    truck: 0,
    driver: 0,
  },
};

export default function OrderManagementAcceptFindingDrivers() {
  const [state, setState] = useImmer(initState);
  const dispatch = useDispatch();
  const [pageCount, setTruckCount] = useState(10);
  const [select, setSelect] = useState("");
  const [truckTypes, truckTypesDispatch] = useReducer(truckTypesReducer, []);
  const [truckOptions, truckOptionsDispatch] = useReducer(
    truckOptionsReducer,
    []
  );
  const [orderInfo, setOrderInfo] = useState("");
  const [truckList, truckListDispatch] = useReducer(truckListReducer, []);
  const [driverList, driverListDispatch] = useReducer(drvierListReducer, []);
  const [garageList, garageListDispatch] = useReducer(garageListReducer, []);
  const [boxValue, setBoxValue] = useState([]);
  const [choosedBox, setChoosedBox] = useState([0, "truck"]);
  const [confirmPossible, setConfirmPossible] = useState(false);
  const history = useHistory();
  const inquiryTarget = useSelector(
    (state) => state.orderManagement.clickTableRow.accept
  );

  const onConfirmClick = async () => {
    const repititionCheck = [];
    try {
      const { order_id } = inquiryTarget;
      const dispatches = [];
      boxValue.map((item) => {
        const dispatch = {
          driver_id: item.driver.id,
          truck_id: item.truck.id,
          garage_id: item.garage.id,
        };
        dispatches.push(dispatch);
      });

      const path = URLPATH.logisDispatch;
      const body = {
        order_id: order_id,
        dispatches: dispatches,
      };
      if (
        repititionCheck.includes(order_id) ||
        repititionCheck.includes(dispatches)
      ) {
        alert("중복된 결과가 있습니다. 다시 선택해주세요.");
        return;
      } else {
        repititionCheck.push(order_id);
        repititionCheck.push(dispatches);
      }

      await defaultPostApiSet(body, path);
      history.push("/orderManagement/accept");
    } catch (err) {}
  };
  const Init = async () => {
    // loading
    setState((draft) => {
      draft.loading = true;
    });
    if (!inquiryTarget) {
      history.push(PATH.orderManagement_accept);
      return;
    }
    const orderInfoPath = `${URLPATH.orderDetail}/` + inquiryTarget.order_id;
    const truckListPath = URLPATH.logisOrderTruckList;
    const truckTypePath = URLPATH.logisOrderTruckTypeList;
    const driverListPath = URLPATH.logisOrderDriverList;
    const garageListaPath = URLPATH.garage;
    const truckListParams = {
      count: pageCount,
      page: 1,
    };
    const driverListParams = {
      count: pageCount,
      page: 1,
    };
    const garageListParams = {
      count: 10,
    };
    try {
      const res = await Promise.all([
        defaultGetApiSet("", orderInfoPath),
        defaultGetApiSet(truckListParams, truckListPath),
        defaultGetApiSet("", truckTypePath),
        defaultGetApiSet(driverListParams, driverListPath),
        defaultGetApiSet(garageListParams, garageListaPath),
      ]);
      setConfirmPossible(false);
      setOrderInfo(res[0].data);
      truckListDispatch(res[1].data);
      truckTypesDispatch(res[2].data);
      driverListDispatch(res[3].data);
      garageListDispatch(res[4].data);
      setSelect({
        truck: null,
        option: null,
        garage: null,
      });
      setBoxValue(() => {
        const choose = [];
        for (let i = 0; i < res[0].data.truck_count; i++) {
          choose.push({
            truck: {
              id: "",
              number: "",
              select: false,
              type: "truck",
              idx: -1,
            },
            driver: {
              id: "",
              name: "",
              select: false,
              type: "driver",
              idx: -1,
            },
            garage: {
              id: "",
              select: false,
            },
          });
        }
        return choose;
      });
    } catch (err) {}
  };
  useEffect(() => {
    Init();
    setTimeout(() => {
      setState((draft) => {
        draft.loading = false;
      });
    }, 500);
    return () => {
      setState((draft) => {
        draft = initState;
      });
      // dispatch(orderManagementClickRowSetInit());
    };
  }, []);

  useEffect(() => {
    const Init = async () => {
      if (select.truck) {
        try {
          const path = URLPATH.logisOrderOptionTpyeList;
          const res = await defaultGetApiSet({ type_id: select.truck }, path);
          truckOptionsDispatch(res.data);
        } catch (err) {}
      }
    };
    Init();
  }, [select.truck]);

  const onReset = (index) => {
    const newArr = boxValue;
    const newTruckObj = truckList;
    const newDriverObj = driverList;
    const newGarageObj = garageList;
    const truckIdx = newArr[index].truck.idx;
    const driverIdx = newArr[index].driver.idx;
    const initBoxValue = {
      truck: {
        id: "",
        number: "",
        select: false,
        type: "truck",
        idx: -1,
      },
      driver: {
        id: "",
        name: "",
        select: false,
        type: "driver",
        idx: -1,
      },
      garage: {
        id: "",
        select: false,
      },
    };

    newArr[index] = initBoxValue;
    if (truckIdx !== -1) {
      newTruckObj.data[truckIdx].select = false;
    }
    if (driverIdx !== -1) {
      newDriverObj.data[driverIdx].select = false;
    }

    truckListDispatch(newTruckObj);
    driverListDispatch(newDriverObj);
    garageListDispatch(newGarageObj);
    setBoxValue(newArr);
    setConfirmPossible(false);
  };
  const onSelect = (idx) => (garageId) => {
    const newArr = [...boxValue];
    newArr[idx].garage.id = garageId;
    newArr[idx].garage.select = true;
    setBoxValue(newArr);
  };
  useEffect(() => {
    boxValue.map((item, index) => {
      const { truck, driver, garage } = item;
      if (truck.number && driver.name && garage.id) {
        setConfirmPossible(true);
      } else {
        setConfirmPossible(false);
      }
    });
  }, [boxValue]);

  const infos = (key, value) => {
    return (
      <div>
        <Styled.Text
          fontsize="0.875rem"
          marginbottom="6px"
          color={colors.white}
          fontweight="600"
        >
          {key}
        </Styled.Text>
        <Styled.InfoBox>{value}</Styled.InfoBox>
      </div>
    );
  };

  return (
    <>
      {state.loading ? (
        <Loading loading={state.loading} height="100vh" />
      ) : (
        <Styled.Root>
          <Styled.StickyPart>
            <Styled.OrderInfoWrapper>
              <div className="header">
                <Styled.Badge>Pick-Up</Styled.Badge>
                <Styled.Text
                  fontsize="1.875rem"
                  color={colors.white}
                  fontweight="600"
                >
                  {moment
                    .utc(inquiryTarget?.location.pu_datetime)
                    .format("DD MMM, hh:mm A")}
                </Styled.Text>
              </div>

              <Styled.OrderInfo>
                {infos("Product", orderInfo && orderInfo.goods[0].info)}
                {infos("Pick-Up Point", orderInfo && orderInfo.pu_city)}
                {infos("Prop-Off Point", orderInfo && orderInfo.do_city)}
                {infos("Turck Type", orderInfo && orderInfo.truck_type_en)}
                {infos("Option", orderInfo && orderInfo.truck_option_en)}
                {infos("Number", orderInfo && orderInfo.truck_count)}
              </Styled.OrderInfo>
            </Styled.OrderInfoWrapper>
            <Styled.GroupSelectWrapper>
              {boxValue &&
                boxValue.map((item, index) => {
                  return (
                    <div key={index}>
                      <Styled.GroupContent>
                        <Styled.Text fontsize="0.875rem" fontweight="600">
                          {index === 0 ? "Leader Group" : `Group ${index + 1}`}
                        </Styled.Text>
                        <Styled.RestIcon
                          src={resetIcon}
                          alt="Delete Icon"
                          onClick={() => onReset(index)}
                        />
                      </Styled.GroupContent>
                      <Styled.SelectBoxWrapper>
                        <div className="top">
                          <SelectBox
                            type="truck"
                            item={item.truck}
                            index={index}
                            choosedBox={choosedBox}
                            setChoosedBox={setChoosedBox}
                          />
                          <SelectBox
                            type="driver"
                            item={item.driver}
                            index={index}
                            choosedBox={choosedBox}
                            setChoosedBox={setChoosedBox}
                          />
                        </div>
                        <Styled.Select
                          className="garage"
                          label="Return Garage"
                          options={garageList.garages}
                          onChange={onSelect(index)}
                          selectGarage={boxValue[index].garage.select}
                          reset={boxValue[index].garage.select === false}
                        />
                      </Styled.SelectBoxWrapper>
                    </div>
                  );
                })}
            </Styled.GroupSelectWrapper>
          </Styled.StickyPart>
          <SelectForm
            state={state}
            tdDatas={truckList}
            innerDispatch={truckListDispatch}
            thDatas={findingTruck}
            truckTypes={truckTypes}
            truckOptions={truckOptions}
            garageList={garageList.garages}
            type="truck"
            select={select}
            setSelect={setSelect}
            pageCount={pageCount}
            boxValue={boxValue}
            setBoxValue={setBoxValue}
            choosedBox={choosedBox}
            setChoosedBox={setChoosedBox}
          />
          <SelectForm
            state={state}
            tdDatas={driverList}
            innerDispatch={driverListDispatch}
            thDatas={findingDriver}
            type="driver"
            pageCount={pageCount}
            boxValue={boxValue}
            setBoxValue={setBoxValue}
            choosedBox={choosedBox}
            setChoosedBox={setChoosedBox}
          />
        </Styled.Root>
      )}
      <Styled.ConfirmButton isPossible={confirmPossible}>
        <input type="button" value="Allocate" onClick={onConfirmClick} />
      </Styled.ConfirmButton>
    </>
  );
}
