import * as React from "react";
import styled from "styled-components";
import { useImmer } from "use-immer";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import Table from "../../../components/table/Table";
import Pagination from "../../../components/pagination";
import { OrderCompleted } from "../../../assets/data/orderManagement/tableData";
import { PATH, URLPATH } from "../../../assets/data/common";
import OrderFilterWrap from "../../../components/pages/order/OrderFilterWrap";
import { calcTotalPage } from "../../../utils/convertData";
import {
  orderManagementAccountClickRow,
  orderManagementLoading,
} from "../../../store/reducers/orderManagementSlice";
import { defaultGetApiSet } from "../../../utils/api";
import LoadingWrap from "../../../components/loading/LoadingWrap";

const DispatchManagementAccountStyle = styled.div`
  background-color: #fff;

  .search-result {
    font-size: 14px;
    color: rgba(124, 124, 124, 0.5);
    padding: 15px 100px;
    background-color: #fff;
  }
`;

const PagenationStyle = styled(Pagination)`
  margin-top: 46px;
  justify-content: flex-end;
  margin-right: 100px;
`;

const initState = {
  pageData: {
    orders: [],
    pageInfo: {
      totalData: null,
      currentPage: null,
      dataPerPage: null,
      totalPages: null,
      startRow: null,
    },
  },
  date: {
    from: null,
    to: null,
  },
  status: null,
};

export default function OrderManagementAccount() {
  const [state, setState] = useImmer(initState);
  const history = useHistory();
  const dispatch = useDispatch();

  const clickRow = (row) => {
    dispatch(orderManagementAccountClickRow({ row }));
    switch (row.state.toString()) {
      case "0":
        return history.push(PATH.orderManagement_account_waiting);
      case "1":
        return history.push(PATH.orderManagement_account_completed);
      default:
        return;
    }
  };

  const clickPagination = async (page) => {
    dispatch(orderManagementLoading(true));
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
      filter: state.status,
      page: page,
    };
    await orderRequestApi(req);
    dispatch(orderManagementLoading(false));
  };

  const onSearch = async () => {
    dispatch(orderManagementLoading(true));
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
      filter: state.status,
    };
    await orderRequestApi(req);
    dispatch(orderManagementLoading(false));
  };

  const orderRequestApi = async (req) => {
    dispatch(orderManagementLoading(true));
    const res = await defaultGetApiSet(req, URLPATH.orderCompleted);
    setState((draft) => {
      draft.pageData = res.data;
    });
    dispatch(orderManagementLoading(false));
  };

  const resetApi = async () => {
    const req = {
      count: 10,
    };
    await orderRequestApi(req);
  };

  const totalPage = calcTotalPage(
    state.pageData.pageInfo.totalData,
    state.pageData.pageInfo.dataPerPage
  );

  React.useEffect(() => {
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
      filter: state.status,
    };
    orderRequestApi(req);
  }, []);

  return (
    <LoadingWrap pageSlice="orderManagement">
      <DispatchManagementAccountStyle>
        <OrderFilterWrap
          selectOption={[
            {
              key: 0,
              value: "Waiting",
            },
            {
              key: 1,
              value: "Completed",
            },
          ]}
          setState={setState}
          state={state}
          onSearch={onSearch}
          resetApi={resetApi}
        />
        <div className="search-result">
          Showing{" "}
          {state.pageData.pageInfo.currentPage
            ? state.pageData.pageInfo.currentPage
            : "0"}{" "}
          page of {totalPage ? totalPage : "0"} result
        </div>
        <Table
          data={state.pageData.orders}
          columns={OrderCompleted}
          headAlign="left"
          bodyAlign="left"
          onClick={clickRow}
          page="order_completed"
        />
        <PagenationStyle
          currentPage={state.pageData.pageInfo.currentPage}
          totalData={state.pageData.pageInfo.totalData}
          cb={clickPagination}
        />
      </DispatchManagementAccountStyle>
    </LoadingWrap>
  );
}
