import React, { useState, useReducer, useEffect } from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import { Select } from "../../../components/search";
import plus from "../../../assets/img/plus.svg";
import { UploadInput } from "../../../components/pages";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { PATH, URLPATH } from "../../../assets/data/common";
import { vehicleManagementLoading } from "../../../store/reducers/vehicleManagementSlice";
import { defaultGetApiSet, defaultPutApiSet } from "../../../utils/api";
import { orderManagementClickRowSetInit } from "../../../store/reducers/orderManagementSlice";
import {
  truckTypesReducer,
  truckOptionsReducer,
  garageListReducer,
} from "../../../hooks/useReducer";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import { InputWrapper, InputContent } from "../../../style/vehicleManagement";
import { registorInitState } from "../../../assets/data/vehicleManagement";
import PortalModal from "../../../components/modal/Portal";
import { Check } from "../../../components/modal/custom";

const Styled = {
  VehicleManagementRegistration: styled.form`
    padding: 66px 90px 61px;
    background-color: #fff;
    .page-label {
      font-size: 1.88rem;
      font-weight: bold;
    }

    .input-label {
      font-size: 0.88rem;
      color: #939393;
      margin-bottom: 11px;
    }

    .upload-img-box {
      margin-top: 44px;
      .upload-img {
        position: relative;
        width: 440px;
        height: 184px;
        border: 1px dotted #898989;
        background: url(${plus}) no-repeat center;

        .preview {
          display: block;
          width: 100%;
          height: 100%;
        }

        input {
          position: absolute;
          width: 100%;
          left: 0;
          right: 0;
          bottom: 0;
          top: 0;
          opacity: 0;
          cursor: pointer;
        }
      }
    }

    .type-option-box {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 10px;
      margin-top: 33px;
    }

    .size-input-box {
      margin-top: 20px;
      column-gap: 10px;
    }

    .number-input-box {
      margin-top: 20px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      column-gap: 10px;
    }
    .chassis-registration-box {
      margin-top: 20px;
      display: grid;
      grid-template-columns: 1.3fr 1fr;
      column-gap: 10px;
    }
    .garage-box {
      margin-top: 20px;
    }
    .vehicle-registration {
      margin-top: 40px;
    }

    .submit-wrap {
      display: flex;
      justify-content: flex-end;
      margin-top: 62px;

      .registration-btn {
        width: 138px;
        height: 44px;
        line-height: 44px;
        border-radius: 5px;
        border: none;
        background-color: ${(props) =>
          props.confirmPossible === true ? "#004CF2" : "#B5B5B5"};

        color: #fff;
        font-size: 0.88rem;
        font-weight: bold;
        text-align: center;
        cursor: ${(props) =>
          props.confirmPossible === true ? "pointer" : "unset"};
      }
    }
  `,

  CustomSelect: styled(Select)`
    & > .select {
      background-color: #f7f7f7;
      height: 50px;
      color: ${(props) => (!props.isNone ? "#B5B5B5" : null)};
    }
  `,
};

export default function VehicleManagementModify() {
  const [state, setState] = useImmer(registorInitState);
  const [isUpdate, setIsUpdate] = useState(true);
  const [confirmPossible, setConfirmPossible] = useState(false);
  const isLoading = useSelector((state) => state.vehicleManagement.isLoading);
  const inquiryTarget = useSelector((state) => {
    return state.vehicleManagement.detail;
  });
  const [truckTypes, truckTypesDispatch] = useReducer(truckTypesReducer, []);
  const [truckOptions, truckOptionsDispatch] = useReducer(
    truckOptionsReducer,
    []
  );
  const [garageList, garageListDispatch] = useReducer(garageListReducer, []);
  const history = useHistory();
  const dispatch = useDispatch();

  const onChangeFile = (key) => (file) => {
    if (key === "registrationImg") {
      setState((draft) => {
        draft.registrationplaceHolder = file.name;
      });
    }
    setState((draft) => {
      draft[key] = file;
    });
  };

  const previewOnChangeFile = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    if (!file) return;
    reader.onloadend = () => {
      setState((draft) => {
        draft.profileImg = file;
        draft.profileImgPreview = reader.result;
      });
    };
    reader.readAsDataURL(file);
  };

  const onSelect = (key) => async (value) => {
    setState((draft) => {
      draft[key] = value;
    });
    if (key === "selectTypeId") {
      try {
        const res = await defaultGetApiSet(
          { type_id: value },
          URLPATH.logisOrderOptionTpyeList
        );

        truckOptionsDispatch(res.data);
        setState((draft) => {
          draft.selectOptionId = res.data[0].opt_id;
        });
        setIsUpdate(!isUpdate);
      } catch (e) {
        console.log("error", e);
      }
    }
  };
  const Init = async () => {
    if (!inquiryTarget) {
      history.push(PATH.vehicleManagement);
      return;
    }
    dispatch(vehicleManagementLoading(true));
    const garageListParams = {
      count: 10,
    };
    try {
      const res = await Promise.all([
        defaultGetApiSet("", URLPATH.logisOrderTruckTypeList),
        defaultGetApiSet(
          { type_id: inquiryTarget.datas.type_id },
          URLPATH.logisOrderOptionTpyeList
        ),
        defaultGetApiSet(garageListParams, URLPATH.garage),
      ]);
      truckTypesDispatch(res[0].data);
      truckOptionsDispatch(res[1].data);
      garageListDispatch(res[2].data);
      dispatch(vehicleManagementLoading(false));
    } catch {}

    setState((draft) => {
      draft.selectTypeId = inquiryTarget.datas.truck_type_en;
      draft.selectOptionId = inquiryTarget.datas.opt_id;
      draft.selectGarageId = inquiryTarget.datas.garage_id;
      draft.initSelectOption = inquiryTarget.datas.truck_option_en;
      draft.input.truckNumber = inquiryTarget.datas.regist_num;
      draft.input.garage = inquiryTarget.datas.garage_name;
      draft.input.brand = inquiryTarget.datas.brand;
      draft.input.chassisNumber = inquiryTarget.datas.chassis_number;
      draft.input.model = inquiryTarget.datas.model;
      draft.input.year = inquiryTarget.datas.year;
      draft.input.horizontal = inquiryTarget.datas.width;
      draft.input.vertical = inquiryTarget.datas.depth;
      draft.input.height = inquiryTarget.datas.height;
      draft.profileImgPreview = inquiryTarget.imgPath[0];
      draft.registrationImgPreview = inquiryTarget.imgPath[1];
      draft.profileImg = inquiryTarget.datas.files.truck_photo_id;
      draft.originProfileImg = inquiryTarget.datas.files.truck_photo_id;
      draft.registrationImg = inquiryTarget.datas.files.truck_registration_id;
      draft.originRegistrationImg =
        inquiryTarget.datas.files.truck_registration_id;
      draft.registrationplaceHolder =
        inquiryTarget.datas.files.truck_registration_id;
    });
    dispatch(vehicleManagementLoading(false));
  };

  const modalVisible = (isOpen) => {
    setState((draft) => {
      draft.modal.visible = isOpen;
    });
  };

  const changeModal = (content) => {
    setState((draft) => {
      draft.modal.content = content;
    });
  };

  const onChangeInput = (key) => (e) => {
    const value = e.target.value;
    setState((draft) => {
      draft.input[key] = value;
    });
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    // if (
    //   !state.selectOptionId ||
    //   !state.profileImg ||
    //   !state.registrationImg ||
    //   !state.input.truckNumber
    // ) {
    //   modalVisible(true);
    //   changeModal("alert");
    //   return;
    // }
    const form = new FormData();
    form.append("opt_id", state.selectOptionId);
    form.append("width", state.input.horizontal);
    form.append("depth", state.input.vertical);
    form.append("height", state.input.height);
    form.append("regist_num", state.input.truckNumber);
    form.append("year", state.input.year);
    form.append("brand", state.input.brand);
    form.append("chassis_number", state.input.chassisNumber);
    form.append("garage_id", state.selectGarageId);
    form.append("model", state.input.model);

    let deleteString = " "; //  string 으로 해야됨 고치면 x
    if (state.profileImg !== state.originProfileImg) {
      form.append("files", state.profileImg);
      deleteString = inquiryTarget.datas.files.truck_photo_id;
    }
    if (state.registrationImg !== state.originRegistrationImg) {
      form.append("files", state.registrationImg);
      if (deleteString !== " ") {
        deleteString = deleteString + ",";
      }
      deleteString =
        deleteString + inquiryTarget.datas.files.truck_registration_id;
    }
    if (deleteString !== " ") {
      form.append("delete_files", deleteString);
    }

    try {
      dispatch(vehicleManagementLoading(true));
      const path = `${URLPATH.logisTruck}/${inquiryTarget.datas.truck_id}`;
      await defaultPutApiSet(form, path);
      dispatch(vehicleManagementLoading(false));
      history.push(PATH.vehicleManagement_detail);
    } catch (e) {
      console.log("error", e);
      setState((draft) => {
        draft = registorInitState;
      });

      dispatch(vehicleManagementLoading(false));
    }
  };

  useEffect(() => {
    Init();
    return () => {
      setState((draft) => {
        draft = registorInitState;
      });
      dispatch(orderManagementClickRowSetInit());
    };
  }, []);

  useEffect(() => {
    if (
      state.selectOptionId &&
      state.input.horizontal &&
      state.input.vertical &&
      state.input.height &&
      state.input.truckNumber &&
      state.input.year &&
      state.input.brand &&
      state.profileImg &&
      state.registrationImg &&
      state.input.model &&
      state.input.chassisNumber &&
      state.selectGarageId
    ) {
      setConfirmPossible(true);
    } else {
      setConfirmPossible(false);
    }
  }, [state]);
  const modalCont = {
    alert: (
      <Check
        doSomethingBtnText="ok"
        title="입력 값 재확인"
        info="입력된 데이터를 확인해주세요."
      />
    ),
  };
  return (
    <LoadingWrap pageSlice="vehicleManagement">
      {state.modal.visible && (
        <PortalModal visible={state.modal.visible} setVisible={modalVisible}>
          {modalCont[state.modal.content]}
        </PortalModal>
      )}
      <Styled.VehicleManagementRegistration
        onSubmit={onSubmit}
        confirmPossible={confirmPossible}
      >
        <div className="page-label">Truck Information</div>
        <div className="upload-img-box">
          <div className="input-label">Truck Picture</div>
          <div className="upload-img">
            {state.profileImgPreview && (
              <img
                src={state.profileImgPreview}
                className="preview"
                alt="preview"
              />
            )}
            <input
              type="file"
              accept="image/*"
              id="truckImg"
              onChange={previewOnChangeFile}
              // required
              name="truckImg"
            />
          </div>
        </div>
        <div className="type-option-box">
          <InputWrapper>
            <div className="input-label">Truck type</div>
            {state.selectTypeId !== null ? (
              <Styled.CustomSelect
                options={truckTypes}
                onChange={onSelect("selectTypeId")}
                label={
                  state.selectTypeId !== null
                    ? state.selectTypeId
                    : "Select Truck Type"
                }
                update={isUpdate}
                isNone={state.selectTypeId}
              />
            ) : null}
          </InputWrapper>
          <InputWrapper>
            <div className="input-label">Option</div>
            {state.selectOptionId !== null ? (
              <Styled.CustomSelect
                options={truckOptions}
                onChange={onSelect("selectOptionId")}
                disabled={!state.selectTypeId}
                label={
                  state.selectOptionId !== null
                    ? state.selectOptionId
                    : "Select Truck Option"
                }
                update={isUpdate}
                isNone={state.selectOptionId}
              />
            ) : null}
          </InputWrapper>
        </div>
        <div className="size-input-box">
          <InputWrapper>
            <div className="input-label">Size</div>
            <div className="size-input-wrap">
              <InputContent
                type="number"
                placeholder="Horizontal(mm)"
                onChange={onChangeInput("horizontal")}
                name="horizontal"
                value={state.input.horizontal}
                isExist={state.input.horizontal ? true : false}
              />
              <InputContent
                type="number"
                placeholder="Vertical(mm)"
                onChange={onChangeInput("vertical")}
                name="vertical"
                value={state.input.vertical}
                isExist={state.input.vertical ? true : false}
              />
              <InputContent
                type="number"
                placeholder="Height(mm)"
                onChange={onChangeInput("height")}
                name="height"
                value={state.input.height}
                isExist={state.input.height ? true : false}
              />
            </div>
          </InputWrapper>
        </div>
        <div className="number-input-box">
          <InputWrapper>
            <div className="input-label">Model Name</div>
            <div>
              <InputContent
                type="text"
                placeholder="Enter Model Name"
                onChange={onChangeInput("model")}
                name="year"
                value={state.input.model}
                isExist={state.input.model ? true : false}
              />
            </div>
          </InputWrapper>
          <InputWrapper>
            <div className="input-label">Year</div>
            <div>
              <InputContent
                type="number"
                placeholder="Enter year"
                onChange={onChangeInput("year")}
                name="year"
                value={state.input.year}
                isExist={state.input.year ? true : false}
              />
            </div>
          </InputWrapper>
          <InputWrapper>
            <div className="input-label">Brand</div>
            <div>
              <InputContent
                type="text"
                placeholder="Enter Brand Name"
                onChange={onChangeInput("brand")}
                name="brand"
                value={state.input.brand}
                isExist={state.input.brand ? true : false}
              />
            </div>
          </InputWrapper>
        </div>
        <div className="chassis-registration-box">
          <InputWrapper>
            <div className="input-label">Chassis Number</div>
            <div>
              <InputContent
                type="text"
                placeholder="Enter the truck chassis number"
                onChange={onChangeInput("chassisNumber")}
                name="brand"
                value={state.input.chassisNumber}
                isExist={state.input.chassisNumber ? true : false}
              />
            </div>
          </InputWrapper>
          <InputWrapper>
            <div className="input-label">Truck Number</div>
            <div>
              <InputContent
                type="text"
                // required
                placeholder="Enter the truck number"
                onChange={onChangeInput("truckNumber")}
                name="truckNumber"
                value={state.input.truckNumber}
                isExist={state.input.truckNumber ? true : false}
              />
            </div>
          </InputWrapper>
        </div>
        <div className="garage-box">
          <InputWrapper>
            <div className="input-label">Garage</div>
            {state.input.garage !== null ? (
              <Styled.CustomSelect
                options={garageList.garages}
                onChange={onSelect("selectGarageId")}
                label={
                  state.input.garage !== null
                    ? state.input.garage
                    : "Enter Truck Parking Garage"
                }
                update={isUpdate}
                isNone={state.selectGarageId}
              />
            ) : null}
          </InputWrapper>
        </div>
        <div className="vehicle-registration">
          <div className="input-label">Vehicle Registration</div>
          {state.registrationplaceHolder.includes("Upload") ? null : (
            <div className="upload-registration">
              <UploadInput
                setFile={onChangeFile("registrationImg")}
                placeHolder={state.registrationplaceHolder}
                accept="image/*"
              />
            </div>
          )}
        </div>

        <div className="submit-wrap">
          <button
            className="registration-btn"
            type="submit"
            disabled={!confirmPossible}
          >
            Modify
          </button>
        </div>
      </Styled.VehicleManagementRegistration>
    </LoadingWrap>
  );
}
