import React, { useEffect, useState } from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import moment from "moment";
import { PATH, URLPATH } from "../../../assets/data/common";
import { vehicleFormList } from "../../../assets/data/vehicleManagement/formData";
import {
  vehicleManagementLoading,
  vehicleManagementDetail,
  vehicleManagementSurchargeRow,
} from "../../../store/reducers/vehicleManagementSlice";
import {
  defaultPostApiSet,
  defaultGetApiSet,
  defaultDelApiSet,
  defaultPutApiSet,
} from "../../../utils/api";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import ListForm from "../../../components/ListForm/ListForm";
import Loading from "../../../components/loading";
import MenuButton from "../../../components/menuButton";
import {
  Confirm,
  MemoModal,
  FormModal,
} from "../../../components/modal/custom";
import {
  surchargeList,
  allocatedList,
  vehicleDetailMenuList,
} from "../../../assets/data/vehicleManagement/tableData";
import vehicleLocation from "../../../assets/img/vehicleLocation.svg";
import PortalModal from "../../../components/modal/Portal";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import PreviewImg from "../../../components/modal/custom/PreviewImg";
import { number } from "../../../utils/number";
const Styled = {
  Root: styled.div`
    background-color: #fff;
    padding-bottom: 80px;
  `,
  Header: styled.div`
    background-color: #f7f7f7;
    padding: 30px 100px;
    display: flex;
    align-content: center;
    font-weight: bold;
    font-size: 1.875rem;
  `,
  TruckInfoWrapper: styled.div`
    padding: 50px 100px;
    display: grid;
    grid-template-columns: 290px auto;
    /* grid-template-columns: 1fr 5.17fr; */
    column-gap: 50px;
    align-items: center;
  `,
  TruckInfoHeader: styled.div`
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    .top {
      display: flex;
      justify-content: space-between;
      .front {
        display: flex;
        column-gap: 1rem;
        align-items: flex-end;
        .status {
          font-size: 1.875rem;
          font-weight: bold;
        }
        .number {
        }
      }
    }
    .buttonGroup {
      display: flex;
      align-items: center;
      column-gap: 50px;
      font-size: 0.875rem;

      .button {
        color: #5786ed;
        :hover {
          cursor: pointer;
          text-decoration: underline;
        }
      }
    }
    .location {
      color: #aac2f6;
      display: flex;
      align-items: center;
      column-gap: 6px;
    }
  `,
  TruckInfoDesc: styled.div`
    margin-top: 66px;
    .title {
      font-size: 1.25rem;
      color: #4d4d4d;
    }
    .descDetail {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      column-gap: 60px;
      row-gap: 24px;
      margin-top: 22px;
    }
  `,
  DescOutlineWrapper: styled.div`
    display: flex;
    flex-direction: column;
    row-gap: 11px;
    font-size: 0.875rem;
    .title {
      font-weight: bold;
    }
  `,
  Img: styled.img`
    width: 100%;
  `,
  Status: styled.div`
    color: ${(props) => props.color};
  `,
};

const initState = {
  truckType: [],
  truckOption: [],
  modal: {
    content: "",
    visible: false,
  },
  upload: {
    surchange: false,
    dispatch: false,
  },
  needUpdate: false,
  menuList: vehicleDetailMenuList,
};

export default function VehicleManagementDetail() {
  const [state, setState] = useImmer(initState);
  const [imgPath, setImgPath] = useState({
    profile: "",
    registration: "",
  });

  const dispatch = useDispatch();
  const history = useHistory();
  const inquiryTarget = useSelector(
    (state) => state.vehicleManagement.clickTableRow
  );
  const surchargeRow = useSelector(
    (state) => state.vehicleManagement.surchargeRow
  );
  const infoList = [
    {
      title: "Regist date",
      content: moment
        .utc(state.truckType.created)
        .hour(9)
        .format("DD MMM, YYYY"),
    },
    { title: "Model type", content: state.truckType.truck_type_en },
    { title: "Option", content: state.truckType.truck_option_en },
    { title: "Model Name", content: state.truckType.model },
    { title: "Year", content: state.truckType.year },
    { title: "Brand", content: state.truckType.brand },
    { title: "Chassis Number", content: state.truckType.chassis_number },
    {
      title: "Size",
      content: `${number(state.truckType.width)}mm X ${number(
        state.truckType.height
      )}mm X ${number(state.truckType.depth)}mm`,
    },
  ];
  const Init = async () => {
    setState((draft) => {
      draft.loading = true;
    });
    if (!inquiryTarget) {
      history.push(PATH.vehicleManagement);
      return;
    }
    try {
      const res = await defaultGetApiSet(
        "",
        `${URLPATH.logisTruck}/${inquiryTarget.id}`
      );
      const {
        s3_folder,
        truck_photo_id,
        truck_registration_id,
      } = res.data.files;
      const profilePath = URLPATH.s3Img + `/${s3_folder}/${truck_photo_id}`;
      const registrationPath =
        URLPATH.s3Img + `/${s3_folder}/${truck_registration_id}`;
      const s3Res = await Promise.all([
        defaultGetApiSet("", profilePath),
        defaultGetApiSet("", registrationPath),
      ]);

      setImgPath({
        profile: `${s3Res[0].config.baseURL}${s3Res[0].config.url}`,
        registration: `${s3Res[1].config.baseURL}${s3Res[1].config.url}`,
      });

      setState((draft) => {
        draft.truckType = res.data;
        res.data.status === 3
          ? (draft.menuList[4].status = true)
          : (draft.menuList[3].status = true);
      });

      const datas = {
        imgPath: [
          `${s3Res[0].config.baseURL}${s3Res[0].config.url}`,
          `${s3Res[1].config.baseURL}${s3Res[1].config.url}`,
        ],
        datas: res.data,
      };
      dispatch(vehicleManagementDetail(datas));
    } catch (err) {}
  };

  React.useEffect(() => {
    try {
      dispatch(vehicleManagementLoading(true));
      Init();
      dispatch(vehicleManagementLoading(false));
      setState((draft) => {
        draft.needUpdate = false;
      });
    } catch (e) {
      dispatch(vehicleManagementLoading(false));
    }
  }, [state.needUpdate]);

  useEffect(() => {
    Init();
    setTimeout(() => {
      setState((draft) => {
        draft.loading = false;
      });
    }, 500);
    return () => {
      setState((draft) => {
        draft = initState;
      });
    };
  }, []);

  const DescOutline = (props) => {
    const { title, content } = props;
    return (
      <Styled.DescOutlineWrapper>
        <div className="title">{title}</div>
        <div>{content}</div>
      </Styled.DescOutlineWrapper>
    );
  };
  const Icon = () => {
    return <MoreVertIcon />;
  };
  const deleteDriver = async () => {
    try {
      dispatch(vehicleManagementLoading(true));
      const path = URLPATH.logisTruck;
      const body = [state.truckType.truck_id];
      await defaultDelApiSet(body, path);

      modalVisible(false);
      dispatch(vehicleManagementLoading(false));
      window.location.reload();
    } catch (e) {
      modalVisible(false);
      dispatch(vehicleManagementLoading(false));
    }
  };
  const modalVisible = (isOpen) => {
    setState((draft) => {
      draft.modal.visible = isOpen;
    });
  };

  const modalChange = (cont) => {
    setState((draft) => {
      draft.modal.content = cont;
    });
  };

  const onDelete = (e) => {
    e.preventDefault();
    modalChange("delete");
    modalVisible(true);
  };
  const onMemo = (e) => {
    e.preventDefault();
    modalChange("memo");
    modalVisible(true);
  };
  const onAdd = (e) => {
    e.preventDefault();
    modalChange("add");
    modalVisible(true);
  };

  const onRegistration = () => {
    modalChange("registration");
    modalVisible(true);
  };

  const openModifyMain = (e, item) => {
    dispatch(vehicleManagementSurchargeRow(item));
    modalChange("modalModify");
    modalVisible(true);
  };

  const openDelete = (e, item) => {
    dispatch(vehicleManagementSurchargeRow(item));
    modalChange("modalDelete");
    modalVisible(true);
  };

  const deleteMaintenance = async () => {
    const path = `${URLPATH.logisTruck}/${state.truckType.truck_id}/surcharge/${surchargeRow.surchargeId}`;
    try {
      await defaultDelApiSet("", path);
      setState((draft) => {
        draft.upload.surchange = !draft.upload.surchange;
      });
      modalVisible(false);
    } catch (e) {
      modalVisible(false);
    }
  };

  const modifySurcharge = async (formDataList) => {
    const path = `${URLPATH.logisTruck}/${state.truckType.truck_id}/surcharge/${surchargeRow.surchargeId}`;
    try {
      const formData = new FormData();
      formData.append("content", formDataList.content);
      formData.append("cost", formDataList.cost);
      formData.append("place", formDataList.place);
      formData.append("memo", formDataList.memo);
      if (formDataList.files !== undefined) {
        formData.append("files", formDataList.files);
        formData.append("delete_files", surchargeRow.file_id);
      }

      await defaultPutApiSet(formData, path);
      setState((draft) => {
        draft.upload.surchange = !draft.upload.surchange;
      });
      modalVisible(false);
    } catch (e) {
      console.log(e);
    }
  };

  const addSurcharge = async (formDataList) => {
    const path = `${URLPATH.logisTruck}/${state.truckType.truck_id}/surcharge`;
    try {
      const formData = new FormData();
      formData.append("content", formDataList.content);
      formData.append("cost", formDataList.cost);
      formData.append("place", formDataList.place);
      formData.append("memo", formDataList.memo);
      formData.append("files", formDataList.files);

      await defaultPostApiSet(formData, path);
      setState((draft) => {
        draft.upload.surchange = !draft.upload.surchange;
      });
      modalVisible(false);
    } catch {}
  };
  const onMemoSubmit = async (memo) => {
    const form = new FormData();
    form.append("memo", memo);
    try {
      dispatch(vehicleManagementLoading(true));
      await defaultPutApiSet(
        form,
        `${URLPATH.logisTruck}/${inquiryTarget.id}/memo`
      );
      dispatch(vehicleManagementLoading(false));
      modalVisible(false);
      setState((draft) => {
        draft.needUpdate = true;
      });
    } catch (e) {
      dispatch(vehicleManagementLoading(false));
      modalVisible(false);
    }
  };
  const onClickStatus = async (label) => {
    const path = `${URLPATH.logisTruck}/${state.truckType.truck_id}/status`;
    const formData = new FormData();
    let status = state.truckType.status;
    switch (label) {
      case "Available":
        if (state.truckType.status === 3) {
          formData.append("status", 0);
          status = 0;
        } else {
          formData.append("status", state.truckType.status);
        }
        break;
      default:
        formData.append("status", 3);
        status = 3;
        break;
    }
    try {
      await defaultPutApiSet(formData, path);
      setState((draft) => {
        draft.truckType.status = status;
        if (status === 3) {
          draft.menuList[3].status = false;
          draft.menuList[4].status = true;
        } else {
          draft.menuList[3].status = true;
          draft.menuList[4].status = false;
        }
      });
    } catch (err) {}
  };
  const modalCont = {
    delete: (
      <Confirm
        title="Are you sure you want to delete this truck?"
        info="If you delete a truck, the truck will not be visible in the vehicle management."
        doSomethingBtnText="Delete"
        doSomethingColor="#FF2424"
        doSomething={deleteDriver}
        onCancel={() => modalVisible(false)}
      />
    ),
    memo: (
      <MemoModal
        memoData={state.truckType?.memo}
        onMemoSubmit={onMemoSubmit}
        label="Vehicle Memo"
        desc="Write down a note about the truck"
        placeholder="There are no memo for this vehicle"
      />
    ),
    add: (
      <FormModal
        title="Add Maintenance Cost"
        formList={vehicleFormList}
        seccesLabel="Add a Cost"
        goSubmit={addSurcharge}
        buttonWidth="132px"
        onCancel={() => modalVisible(false)}
      />
    ),
    registration: <PreviewImg imgPath={imgPath.registration} />,
    modalModify: (
      <FormModal
        title="Modify Maintenance Cost"
        formList={vehicleFormList}
        seccesLabel="Modify"
        goSubmit={modifySurcharge}
        buttonWidth="132px"
        onCancel={() => modalVisible(false)}
        initValue={surchargeRow}
      />
    ),
    modalDelete: (
      <Confirm
        title="Are you sure you want to delete this Cost?"
        info="This will delete your maintenance cost from mangement. Are you sure?"
        doSomethingBtnText="Delete"
        doSomethingColor="#FF2424"
        doSomething={deleteMaintenance}
        onCancel={() => modalVisible(false)}
      />
    ),
  };
  const Status = (props) => {
    const { status } = props;
    const label = [
      { content: "Wating", color: "#FF9B45" },
      { content: "On the way back", color: "#5786ED" },
      { content: "Shipping", color: "#03D078" },
      { content: "Unavaliable", color: "#FF7474" },
    ];

    return (
      <Styled.Status color={label[status]?.color}>
        {label[status]?.content}
      </Styled.Status>
    );
  };
  return (
    <LoadingWrap pageSlice="vehicleManagement">
      <Styled.Root>
        {state.modal.visible && (
          <PortalModal visible={state.modal.visible} setVisible={modalVisible}>
            {modalCont[state.modal.content]}
          </PortalModal>
        )}
        <Styled.Header>Vehicle Management Detail</Styled.Header>
        <Styled.TruckInfoWrapper>
          <Styled.Img src={imgPath.profile} alt="truck Img" />
          <div>
            <Styled.TruckInfoHeader>
              <div className="top">
                <div className="front">
                  <div className="status">
                    <Status status={state.truckType.status} />
                  </div>

                  <div className="number">{state.truckType.regist_num}</div>
                </div>
                <div className="buttonGroup">
                  <div className="button" onClick={onRegistration}>
                    Vehicle Registartion
                  </div>
                  <div className="button" onClick={onMemo}>
                    Memo
                  </div>
                  <MenuButton
                    menuList={state.menuList}
                    Icon={Icon}
                    onDelete={onDelete}
                    onClickStatus={onClickStatus}
                  />
                </div>
              </div>
              <div className="location">
                <img src={vehicleLocation} alt="location Img" />
                {state.truckType.garage_name}
              </div>
            </Styled.TruckInfoHeader>
            <Styled.TruckInfoDesc>
              <div className="title">Vehicle Infomation</div>
              <div className="descDetail">
                {infoList.map((item, index) => {
                  const { title, content } = item;
                  return (
                    <DescOutline title={title} content={content} key={index} />
                  );
                })}
              </div>
            </Styled.TruckInfoDesc>
          </div>
        </Styled.TruckInfoWrapper>
        {state.truckType.truck_id ? (
          <>
            <ListForm
              truckId={state.truckType.truck_id}
              thDatas={surchargeList}
              type="surcharge"
              onAdd={onAdd}
              upload={state.upload.surchange}
              openModifyMain={openModifyMain}
              openDelete={openDelete}
            />
            <ListForm
              truckId={state.truckType.truck_id}
              thDatas={allocatedList}
              type="dispatch"
              upload={state.upload.dispatch}
            />
          </>
        ) : null}
      </Styled.Root>
    </LoadingWrap>
  );
}
