import * as React from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import CalendarCompo from "../../../components/calendar";
import CommonFilterCompo from "../../../components/CommonFilterCompo";
import Table from "../../../components/table/Table";
import PageLabel from "../../../components/PageLabel";
import moment from "moment";
import { calcTotalPage, changeUTCtime } from "../../../utils/convertData";
import { DriverManagementDetailCol } from "../../../assets/data/driverManagement/tableData";
import {
  driverManagementClickRowSetInit,
  driverManagementLoading,
  driverManagementModifyTarget,
} from "../../../store/reducers/driverManagementSlice";
import {
  defaultDelApiSet,
  defaultGetApiSet,
  defaultPutApiSet,
} from "../../../utils/api";
import { DEPTH, PATH, URLPATH } from "../../../assets/data/common";
import Pagenation from "../../../components/pagination";
import { useHistory } from "react-router-dom";
import { s3ConverData } from "../../../utils/s3Convert";
import upArrow from "../../../assets/img/upArrow.svg";
import dotbtn from "../../../assets/img/dotbtn.svg";
import PortalModal from "../../../components/modal/Portal";
import { Confirm, MemoModal } from "../../../components/modal/custom";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import { useDidUpdate } from "../../../hooks/useDidUpdate";
import PreviewImg from "../../../components/modal/custom/PreviewImg";

const Styled = {
  DriverManagementDetail: styled.div`
    padding-bottom: 50px;
    min-width: 1024px;

    .page-label-box {
      display: flex;
      align-items: center;
      padding: 0 100px;
      background-color: #f7f7f7;
      height: 100px;
      border-bottom: 1px solid #e3e3e3;
    }

    .search-result {
      font-size: 14px;
      color: rgba(124, 124, 124, 0.5);
      padding: 15px 100px;
      background-color: #fff;
    }
  `,
  DriverProfileInfo: styled.div`
    display: flex;
    position: relative;
    align-items: center;
    height: 200px;
    background-color: #fff;
    border-bottom: 1px solid #e3e3e3;
    padding: 0 100px;

    .profile-img {
      width: 140px;
      height: 140px;
      border: 1px dotted #707070;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    .profile-info {
      margin-left: 60px;

      .personal,
      .work-info {
        display: flex;
        flex-direction: row;
      }
      .sub {
        margin-top: 15px;
      }
      .info + .info {
        margin-left: 88px;
      }

      .personal {
        .main {
          font-size: 1.25rem;
          font-weight: bold;
        }
        .sub {
          font-size: 0.88rem;
          color: #898989;
        }
      }
      .work-info {
        margin-top: 41px;
        .main {
          font-size: 0.88rem;
          font-weight: bold;
        }
        .sub {
          font-size: 0.88rem;
        }
      }
    }

    .button-wrap {
      position: absolute;
      top: 37px;
      right: 100px;
      display: flex;

      & > .popup-btn {
        color: #5786ed;
        text-decoration: underline;
      }
      & > .popup-btn + .popup-btn {
        margin-left: 49px;
      }

      .more-btn-wrap {
        position: relative;
        & > .toggle-btn {
          width: 20px;
          height: 20px;
          margin-left: 61px;
          background: url(${dotbtn}) no-repeat center;
          border: none;
          text-indent: -9999px;
        }
        .btn-menu-list {
          position: absolute;
          right: 0;
          border: 1px solid #e3e3e3;
          border-radius: 5px;
          background-color: #fff;
          border: 1px solid #e3e3e3;
          visibility: hidden;
          z-index: ${DEPTH.high};

          li > a {
            display: block;
            padding: 10px 18px;
            white-space: nowrap;
            background-color: #fff;
            color: #b5b5b5;
          }
          li:hover > a {
            color: #5786ed;
            background-color: #eef3fd;
          }
        }
        &.on .btn-menu-list {
          visibility: visible;
        }
      }
    }
  `,
  CollapseWrap: styled.div`
    .collapse-label {
      display: flex;
      padding: 0 100px;
      height: 68px;
      align-items: center;
      background-color: #f7f7f7;
      border-bottom: 1px solid #e3e3e3;
      justify-content: space-between;
      cursor: pointer;

      .label {
        font-size: 1.25rem;
      }
      .collapse-btn {
        width: 20px;
        height: 20px;
        background: url(${upArrow}) no-repeat center;
        border: none;
      }
    }
    .collapse-body {
      transition: all 0.3s ease-out;
      height: ${(props) => `${props.collapseContHeihgt}px`};
      overflow: hidden;

      &.close {
        height: 0;
      }
    }
  `,
  PagenationStyle: styled(Pagenation)`
    margin-top: 46px;
    justify-content: flex-end;
    margin-right: 100px;
  `,
};

const initState = {
  profileData: null,
  profileImg: null,
  driverLicenseImg: null,
  tableData: {
    dispatches: [],
    pageInfo: {
      totalData: null,
      currentPage: null,
      dataPerPage: null,
      totalPages: null,
      startRow: null,
    },
  },
  date: {
    from: null,
    to: null,
  },
  openMenu: false,
  toggleCollapse: false,
  modal: {
    content: "",
    visible: false,
  },
  needUpdate: false,
};

export default function DriverManagementDetail() {
  const [searchDisabled, setSearchDisabled] = React.useState(true);
  const [state, setState] = useImmer(initState);
  const dispatch = useDispatch();
  const history = useHistory();
  const inquiryTarget = useSelector(
    (state) => state.driverManagement.clickTableRow
  );
  const ref = React.useRef();
  const collapseRef = React.useRef(null);
  const openMenuRef = React.useRef(false);

  const onToggleMenu = () => {
    openMenuRef.current = !openMenuRef.current;
    setState((draft) => {
      draft.openMenu = openMenuRef.current;
    });
  };

  const handleClickOutside = (e) => {
    const target = e.target;
    if (openMenuRef.current && ref.current && !ref.current.contains(target)) {
      openMenuRef.current = false;
      setState((draft) => {
        draft.openMenu = openMenuRef.current;
      });
    }
  };

  const getDate = (key) => (date) => {
    setState((draft) => {
      draft.date[key] = date;
    });
  };

  const getContHeihgt = () => {
    const _height = collapseRef.current && collapseRef.current.clientHeight;
    return _height;
  };

  const driverDetailProfileApi = async () => {
    if (!inquiryTarget) {
      history.push(PATH.driverManagement);
      return;
    }
    const res = await defaultGetApiSet(
      "",
      `${URLPATH.logisDriver}/${inquiryTarget.driver_id}`
    );

    res.data.profile &&
      s3ConverData(res.data.s3_folder, res.data.profile).then((img) => {
        setState((draft) => {
          draft.profileImg = img;
        });
      });

    setState((draft) => {
      draft.profileData = res.data;
    });

    res.data.driver_license &&
      s3ConverData(res.data.s3_folder, res.data.driver_license).then((img) => {
        setState((draft) => {
          draft.driverLicenseImg = img;
        });
      });
  };

  const driverDetailTableApi = async (req) => {
    if (!inquiryTarget) {
      history.push(PATH.driverManagement);
      return;
    }
    const res = await defaultGetApiSet(
      req,
      `${URLPATH.logisDriver}/${inquiryTarget.driver_id}/dispatch`
    );
    console.log("res.data", res.data);
    setState((draft) => {
      draft.tableData = res.data;
    });
  };

  const modalVisible = (isOpen) => {
    setState((draft) => {
      draft.modal.visible = isOpen;
    });
  };
  const modalChange = (cont) => {
    setState((draft) => {
      draft.modal.content = cont;
    });
  };

  const onDelete = (e) => {
    e.preventDefault();
    modalChange("delete");
    modalVisible(true);
  };

  const onMemo = (e) => {
    e.preventDefault();
    modalChange("memo");
    modalVisible(true);
  };

  const onLicense = (e) => {
    e.preventDefault();
    modalChange("license");
    modalVisible(true);
  };

  const deleteDriver = async () => {
    try {
      dispatch(driverManagementLoading(true));
      const res = await defaultDelApiSet(
        "",
        `${URLPATH.logisDriver}/${inquiryTarget.driver_id}`
      );
      modalVisible(false);
      dispatch(driverManagementLoading(false));
      history.push(PATH.driverManagement);
    } catch (e) {
      modalVisible(false);
      dispatch(driverManagementLoading(false));
    }
  };

  const onModify = async (e) => {
    e.preventDefault();
    dispatch(driverManagementModifyTarget({ target: inquiryTarget.driver_id }));
    history.push(PATH.driverManagement_modify);
  };

  const onSearch = async () => {
    dispatch(driverManagementLoading(true));
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
    };
    await driverDetailTableApi(req);
    dispatch(driverManagementLoading(false));
  };

  const clickPagination = async (page) => {
    dispatch(driverManagementLoading(true));
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
      page: page,
    };
    await driverDetailTableApi(req);
    dispatch(driverManagementLoading(false));
  };

  const onMemoSubmit = async (memo) => {
    const form = new FormData();
    form.append("memo", memo);
    try {
      dispatch(driverManagementLoading(true));
      const res = await defaultPutApiSet(
        form,
        `${URLPATH.logisDriver}/${inquiryTarget.driver_id}/memo`
      );
      dispatch(driverManagementLoading(false));
      modalVisible(false);
      setState((draft) => {
        draft.needUpdate = true;
      });
    } catch (e) {
      console.log("error", e);
      dispatch(driverManagementLoading(false));
      modalVisible(false);
    }
  };

  const onReset = () => {
    setSearchDisabled(true);
    if (initState.date !== state.date) {
      setState((draft) => {
        draft.date.from = null;
        draft.date.to = null;
      });
    }
  };

  const toggleCollapse = () => {
    console.log("CLCIK");
    setState((draft) => {
      draft.toggleCollapse = !state.toggleCollapse;
    });
  };

  React.useEffect(() => {
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
    };
    try {
      dispatch(driverManagementLoading(true));
      Promise.all([driverDetailProfileApi(), driverDetailTableApi(req)]);
      dispatch(driverManagementLoading(false));
      setState((draft) => {
        draft.needUpdate = false;
      });
    } catch (e) {
      dispatch(driverManagementLoading(false));
    }
  }, [state.needUpdate]);

  React.useEffect(() => {
    window.addEventListener("click", handleClickOutside);

    return () => {
      window.removeEventListener("click", handleClickOutside);
    };
  }, []);

  React.useEffect(() => {
    if (state.date.from !== null && state.date.to !== null) {
      setSearchDisabled(false);
    }
  }, [state.date]);
  const DriverProfileInfo = (
    <Styled.DriverProfileInfo>
      <div className="profile-img">
        {state.profileImg && <img src={state.profileImg} alt="" />}
      </div>
      <div className="profile-info">
        <div className="personal">
          <div className="info">
            <div className="main">
              {state.profileData &&
                `${state.profileData.first_name} ${state.profileData.last_name}`}
            </div>
            <div className="sub">
              {state.profileData && state.profileData.phone}
            </div>
          </div>
        </div>
        <div className="work-info">
          <div className="info">
            <div className="main">Last Shipping</div>
            <div className="sub">
              {state.profileData?.last_shipping_date &&
                changeUTCtime(state.profileData.last_shipping_date, 0).format(
                  "DD MMM, YYYY"
                )}
            </div>
          </div>
          <div className="info">
            <div className="main">Registration Date</div>
            <div className="sub">
              {state.profileData?.created &&
                changeUTCtime(state.profileData.created, 0).format(
                  "DD MMM, YYYY"
                )}
            </div>
          </div>
        </div>
      </div>
      <div className="button-wrap">
        <a href="#" className="popup-btn" onClick={onLicense}>
          View License
        </a>
        <a href="#" className="popup-btn" onClick={onMemo}>
          Memo
        </a>
        <div
          className={`more-btn-wrap ${state.openMenu ? "on" : ""}`}
          ref={ref}
        >
          <button className="toggle-btn" onClick={onToggleMenu}>
            more
          </button>
          <ul className="btn-menu-list">
            <li>
              <a href="#" onClick={onDelete}>
                Delete
              </a>
            </li>
            <li>
              <a href="#" onClick={onModify}>
                Modify
              </a>
            </li>
          </ul>
        </div>
      </div>
    </Styled.DriverProfileInfo>
  );

  const DateCompo = [
    <CalendarCompo
      placeholder="Pick-Up Date"
      getDate={getDate("from")}
      parentValue={state.date.from && changeUTCtime(state.date.from, 0)._d}
    />,
    <CalendarCompo
      placeholder="Pick-Up Date"
      getDate={getDate("to")}
      parentValue={state.date.to && changeUTCtime(state.date.to, 0)._d}
    />,
  ];

  const totalPage = state.tableData.pageInfo
    ? calcTotalPage(
        state.tableData.pageInfo?.totalData,
        state.tableData.pageInfo?.dataPerPage
      )
    : 0;

  const DriverList = (
    <>
      <CommonFilterCompo
        dateCompo={DateCompo}
        onSearch={onSearch}
        onReset={onReset}
        disabled={searchDisabled}
      />
      <div className="search-result">
        Showing {state.tableData.pageInfo?.currentPage} page of{" "}
        {totalPage ? totalPage : "0"} result
      </div>
      <Table
        data={state.tableData.dispatches}
        columns={DriverManagementDetailCol}
        headAlign="left"
        bodyAlign="left"
      />
      <Styled.PagenationStyle
        currentPage={state.tableData.pageInfo?.currentPage}
        totalData={state.tableData.pageInfo?.totalData}
        cb={clickPagination}
      />
    </>
  );

  const modalCont = {
    delete: (
      <Confirm
        title="Are you sure you want to delete your driver?"
        info="If you delete a driver, the driver will not be visible in the driver management."
        doSomethingBtnText="Delete"
        doSomethingColor="#FF7474"
        doSomething={deleteDriver}
        onCancel={() => modalVisible(false)}
      />
    ),
    memo: (
      <MemoModal
        memoData={state.profileData?.memo}
        onMemoSubmit={onMemoSubmit}
        label="Driver Memo"
        desc="Write down a note about the driver"
        placeholder="There are no memo for this driver"
      />
    ),
    license: <PreviewImg imgPath={state.driverLicenseImg} />,
  };

  const bodyHeihgt = getContHeihgt();

  return (
    <LoadingWrap pageSlice="driverManagement">
      <Styled.DriverManagementDetail>
        {state.modal.visible && (
          <PortalModal visible={state.modal.visible} setVisible={modalVisible}>
            {modalCont[state.modal.content]}
          </PortalModal>
        )}
        <div className="page-label-box">
          <PageLabel imgType="icon_driver" labelText="Driver Management" />
        </div>
        {DriverProfileInfo}
        <Styled.CollapseWrap collapseContHeihgt={bodyHeihgt}>
          <div className="collapse-label" onClick={toggleCollapse}>
            <div className="label">Dispatched List</div>
            <button className="collapse-btn"></button>
          </div>
          <div
            className={`collapse-body ${state.toggleCollapse ? "close" : ""}`}
          >
            <div className="collapse-wrap" ref={collapseRef}>
              {DriverList}
            </div>
          </div>
        </Styled.CollapseWrap>
      </Styled.DriverManagementDetail>
    </LoadingWrap>
  );
}
