import * as React from "react";
import styled from "styled-components";
import { useImmer } from "use-immer";
import { useSelector, useDispatch } from "react-redux";
import PortalModal from "../../../components/modal/Portal";
import { Confirm } from "../../../components/modal/custom";
import dotbtn from "../../../assets/img/dotbtn.svg";
import dotbtnvertical from "../../../assets/img/dotbtnvertical.svg";
import { useHistory } from "react-router-dom";
import { DEPTH, PATH, URLPATH } from "../../../assets/data/common";
import {
  defaultDelApiSet,
  defaultGetApiSet,
  defaultPutApiSet,
  defaultPostApiSet,
} from "../../../utils/api";
import {
  CostValueForm,
  PointBoxCompo,
  ProductInfo,
  TruckInfo,
  MemoInfo,
} from "../../../components/pages";
import {
  orderManagementClickRowSetInit,
  orderManagementLoading,
} from "../../../store/reducers/orderManagementSlice";
import Loading from "../../../components/loading";
import { s3ConverData } from "../../../utils/s3Convert";
import LoadingWrap from "../../../components/loading/LoadingWrap";

const Styled = {
  OrderManagementReqDetail: styled.div`
    .submit-wrap {
      display: flex;
      justify-content: flex-end;
      padding: 0 100px 70px;
      background-color: #fff;

      .send-btn {
        display: inline-block;
        border: none;
        background-color: ${(props) =>
          props.disabled === true ? "#B5B5B5" : "#004cf2"};
        border-radius: 5px;
        padding: 0 48px;
        color: #fff;
        font-size: 1rem;
        font-weight: bold;
        height: 50px;
        line-height: 50px;
        cursor: ${(props) => (props.disabled === true ? "unset" : "pointer")};
      }
    }
  `,
  PageHeader: styled.div`
    background-color: #fff;
    padding: 60px 100px 40px 100px;
    border-bottom: 1px solid #e3e3e3;
    .header-wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .header-title {
      font-size: 1.9rem;
      font-weight: bold;
      line-height: 37px;

      &.send {
        color: #5786ed;
      }
      &.notsend {
      }
    }

    .header-more-btn-wrap {
      position: relative;

      .header-more {
        border: none;
        border-radius: 50%;
        text-indent: -9999px;
        background: url(${dotbtnvertical}) no-repeat center;
        :hover {
          background-color: #e3e3e3;
        }
        width: 40px;
        height: 40px;
      }

      &.on .btn-menu-list {
        visibility: visible;
      }

      .btn-menu-list {
        position: absolute;
        right: 0;
        border: 1px solid #e3e3e3;
        border-radius: 5px;
        background-color: #fff;
        visibility: hidden;
        z-index: ${DEPTH.middle};

        & > li {
          padding: 10px 18px;
          white-space: nowrap;
          min-width: 143px;

          a {
            display: block;
            width: 100%;
            height: 100%;
            font-size: 0.9rem;
            color: #b5b5b5;
          }

          &:hover {
            background-color: #eef3fd;
          }
          &:hover a {
            color: #5786ed;
          }
        }
      }
    }

    .header-subtitle {
      font-size: 1rem;
      line-height: 19px;
      color: #939393;
      margin-top: 6px;
    }
  `,
};

const initState = {
  getData: {},
  costInput: 0,
  openMenu: false,
  modifyMode: false,
  modalVisible: false,
  goodsImg: [],
  driverImg: [],
};

export default function OrderManagementRequestDetail({ pageType }) {
  const [state, setState] = useImmer(initState);
  const [disabled, setDisabled] = React.useState(true);
  const dispatch = useDispatch();
  const history = useHistory();
  const ref = React.useRef();
  const openMenuRef = React.useRef(false);
  const inquiryTarget = useSelector((state) => {
    if (state.orderManagement.clickTableRow.request) {
      return state.orderManagement.clickTableRow.request;
    } else {
      return state.orderManagement.clickTableRow.accept;
    }
  });
  const isLoading = useSelector((state) => state.orderManagement.isLoading);
  const pageData = state.getData;

  const setStartData = async () => {
    let goodsImgStore = [];
    let driverImgStore = [];

    if (!inquiryTarget) {
      history.push(PATH.orderManagement_req);
      return;
    }
    dispatch(orderManagementLoading(true));
    const res = await defaultGetApiSet(
      "",
      `${URLPATH.orderDetail}/${inquiryTarget.order_id}`
    );

    for (const item of res.data.files?.file_id.split(",")) {
      await s3ConverData(res.data.files.s3_folder, item).then((i) => {
        goodsImgStore = [...goodsImgStore, i];
      });
    }

    await s3ConverData(
      res.data.driver_photo?.s3_folder,
      res.data.driver_photo?.pu_photo_id
    )
      .then((i) => {
        driverImgStore = [...driverImgStore, i];
      })
      .catch((e) => {
        driverImgStore = [...driverImgStore, null];
      });
    await s3ConverData(
      res.data.driver_photo?.s3_folder,
      res.data.driver_photo?.do_photo_id
    )
      .then((i) => {
        driverImgStore = [...driverImgStore, i];
      })
      .catch((e) => {
        driverImgStore = [...driverImgStore, null];
      });

    setState((draft) => {
      draft.getData = res.data;
      draft.costInput = res.data.price ? res.data.price : 0;
      draft.goodsImg = goodsImgStore;
      draft.driverImg = driverImgStore;
    });
    dispatch(orderManagementLoading(false));
  };

  const onToggleMenu = () => {
    openMenuRef.current = !openMenuRef.current;
    setState((draft) => {
      draft.openMenu = openMenuRef.current;
    });
  };

  const onCancel = async (e) => {
    e.preventDefault();
    // console.log("click cancel");
    //취소 요청 -> 확인 팝업 -> 테이블 페이지로 이동
    setState((draft) => {
      draft.modalVisible = true;
    });
  };

  const onConfirmCancel = async () => {
    try {
      const res = await defaultDelApiSet(
        "",
        `${URLPATH.orderOffer}/${pageData.offer_id}`
      );
      if (res.data) {
        history.push(PATH.orderManagement_req);
        setState((draft) => {
          draft.modalVisible = false;
        });
      }
    } catch (e) {
      console.log("error", e);
    }
  };

  const onModify = (e) => {
    e.preventDefault();
    //close menu
    openMenuRef.current = false;
    setState((draft) => {
      draft.modifyMode = true;
      draft.openMenu = openMenuRef.current;
    });
  };

  const handleClickOutside = (e) => {
    const target = e.target;
    if (openMenuRef.current && ref.current && !ref.current.contains(target)) {
      openMenuRef.current = false;
      setState((draft) => {
        draft.openMenu = openMenuRef.current;
      });
    }
  };

  const changeApi = async (pageType) => {
    let formData = new FormData();

    switch (pageType) {
      case "write":
        formData.append("order_id", inquiryTarget.order_id);
        formData.append("price", state.costInput);
        return await defaultPostApiSet(formData, URLPATH.orderOffer);
      case "deny":
        formData.append("price", state.costInput);
        return await defaultPutApiSet(
          formData,
          `${URLPATH.orderOffer}/${pageData.offer_id}`
        );
      case "send":
        formData.append("price", state.costInput);
        return await defaultPutApiSet(
          formData,
          `${URLPATH.orderOffer}/${pageData.offer_id}`
        );
      case "aloocated":
        formData.append("price", state.costInput);
        return await defaultPutApiSet(
          formData,
          `${URLPATH.orderOffer}/${pageData.offer_id}`
        );
      default:
        break;
    }
  };

  const onSumbit = async () => {
    setDisabled(true);
    if (disabled === true) {
      return;
    }
    try {
      const res = await changeApi(pageType);
      res.data && history.push(PATH.orderManagement_req);
    } catch (e) {
      console.log("error", e);
    }
  };

  const pageHeaderCompo = (pageType) => {
    switch (pageType) {
      case "write":
        return (
          <Styled.PageHeader>
            <h2 className="header-title notsend">Write a Quote</h2>
            <div className="header-subtitle">{pageData.order_number}</div>
          </Styled.PageHeader>
        );
      case "send":
        return (
          <Styled.PageHeader>
            <div className="header-wrap">
              <h2 className="header-title send">Send a Quote</h2>
              <div
                className={`header-more-btn-wrap ${state.openMenu ? "on" : ""}`}
                ref={ref}
              >
                <button className="header-more" onClick={onToggleMenu}>
                  more
                </button>
                <ul className="btn-menu-list">
                  <li>
                    <a href="#" onClick={onCancel}>
                      Cancel
                    </a>
                  </li>
                  <li>
                    <a href="#" onClick={onModify}>
                      Modify
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <div className="header-subtitle">#{pageData.order_number}</div>
          </Styled.PageHeader>
        );
      case "deny":
        return (
          <Styled.PageHeader>
            <h2 className="header-title deny">Resend a Quote</h2>
            <div className="header-subtitle">{pageData.order_number}</div>
          </Styled.PageHeader>
        );
      default:
        break;
    }
  };

  React.useEffect(() => {
    window.addEventListener("click", handleClickOutside);
    setState((draft) => {
      if (pageType === "write" || pageType === "deny") {
        draft.modifyMode = true;
      }
    });
    setStartData();
    return () => {
      setState((draft) => {
        draft = initState;
      });

      dispatch(orderManagementClickRowSetInit());
      window.removeEventListener("click", handleClickOutside);
    };
  }, []);
  React.useEffect(() => {
    if (state.costInput !== 0 && state.costInput !== "") {
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  }, [state.costInput]);

  return (
    <LoadingWrap pageSlice="orderManagement">
      <Styled.OrderManagementReqDetail disabled={disabled}>
        {state.modalVisible && (
          <PortalModal
            visible={state.modalVisible}
            setVisible={(visible) =>
              setState((draft) => {
                draft.modalVisible = visible;
              })
            }
          >
            <Confirm
              title="Are you sure you want to cancel your quote?"
              info="If you cancel a quote, the quote content will not be visible in the owner platform."
              doSomethingBtnText="Deleted"
              doSomethingColor="#FF7474"
              doSomething={onConfirmCancel}
              onCancel={() =>
                setState((draft) => {
                  draft.modalVisible = false;
                })
              }
            />
          </PortalModal>
        )}
        {pageHeaderCompo(pageType)}

        <PointBoxCompo
          pu_city={pageData.pu_city}
          pu_addr={pageData.pu_addr}
          pu_addr_detail={pageData.pu_addr_detail}
          pu_datetime={pageData.pu_datetime}
          pu_pic={pageData.pu_pic}
          pu_phone={pageData.pu_phone}
          do_city={pageData.do_city}
          do_addr={pageData.do_addr}
          do_addr_detail={pageData.do_addr_detail}
          do_datetime={pageData.do_datetime}
          do_pic={pageData.do_pic}
          do_phone={pageData.do_phone}
          company_name={pageData.cargo?.company_name}
        />
        <ProductInfo
          productInfo={pageData.goods}
          goodsImagePath={state.goodsImg}
          driverImagePath={state.driverImg}
        />
        <TruckInfo
          truckType={pageData.truck_type_en}
          truckCount={pageData.truck_count}
          option={pageData.truck_option_en}
        />
        {pageData.memo && <MemoInfo memo={pageData.memo} />}
        <CostValueForm
          modifyMode={state.modifyMode}
          getValue={setState}
          costValue={state.costInput}
        />
        <div className="submit-wrap">
          {state.modifyMode && (
            <button className="send-btn" onClick={onSumbit}>
              Send
            </button>
          )}
        </div>
      </Styled.OrderManagementReqDetail>
    </LoadingWrap>
  );
}
