import * as React from "react";
import styled from "styled-components";
import { useImmer } from "use-immer";
import { useHistory } from "react-router";
import Table from "../../../components/table/Table";
import Pagination from "../../../components/pagination";
import { OrderBeforeShipping } from "../../../assets/data/orderManagement/tableData";
import { useDispatch } from "react-redux";
import { defaultGetApiSet } from "../../../utils/api";
import {
  orderManagementBeforeShipClickRow,
  orderManagementLoading,
} from "../../../store/reducers/orderManagementSlice";
import { PATH, URLPATH } from "../../../assets/data/common";
import { OrderFilterWrap } from "../../../components/pages";
import { calcTotalPage } from "../../../utils/convertData";
import LoadingWrap from "../../../components/loading/LoadingWrap";

const DispatchManagementBeforeStyle = styled.div`
  background-color: #fff;

  .search-result {
    font-size: 14px;
    color: rgba(124, 124, 124, 0.5);
    padding: 15px 100px;
    background-color: #fff;
  }
`;

const PagenationStyle = styled(Pagination)`
  margin-top: 46px;
  justify-content: flex-end;
  margin-right: 100px;
`;

const initState = {
  pageData: {
    orders: [],
    pageInfo: {
      totalData: null,
      currentPage: null,
      dataPerPage: null,
      totalPages: null,
      startRow: null,
    },
  },
  date: {
    from: null,
    to: null,
  },
};

export default function OrderManagementBefore() {
  const [state, setState] = useImmer(initState);
  const history = useHistory();
  const dispatch = useDispatch();

  const clickRow = (row) => {
    dispatch(orderManagementBeforeShipClickRow({ row }));
    // console.log("row", row.status);
    history.push(PATH.orderManagement_before_detail);
  };

  const clickPagination = async (page) => {
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
      page: page,
    };
    orderRequestApi(req);
  };

  const onSearch = async () => {
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
    };
    orderRequestApi(req);
  };

  const orderRequestApi = async (req) => {
    dispatch(orderManagementLoading(true));
    const res = await defaultGetApiSet(req, URLPATH.orderBeforeShipping);
    setState((draft) => {
      draft.pageData = res.data;
    });
    dispatch(orderManagementLoading(false));
  };

  const resetApi = async () => {
    const req = {
      count: 10,
    };
    await orderRequestApi(req);
  };

  const totalPage = calcTotalPage(
    state.pageData.pageInfo.totalData,
    state.pageData.pageInfo.dataPerPage
  );

  React.useEffect(() => {
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
    };

    orderRequestApi(req);
  }, []);

  return (
    <LoadingWrap pageSlice="orderManagement">
      <DispatchManagementBeforeStyle>
        <OrderFilterWrap
          state={state}
          setState={setState}
          onSearch={onSearch}
          resetApi={resetApi}
          type="B"
        />
        <div className="search-result">
          Showing{" "}
          {state.pageData.pageInfo.currentPage
            ? state.pageData.pageInfo.currentPage
            : "0"}{" "}
          page of {totalPage ? totalPage : "0"} result
        </div>
        <Table
          data={state.pageData.orders}
          columns={OrderBeforeShipping}
          headAlign="left"
          bodyAlign="left"
          onClick={clickRow}
        />
        <PagenationStyle
          currentPage={state.pageData.pageInfo.currentPage}
          totalData={state.pageData.pageInfo.totalData}
          cb={clickPagination}
        />
      </DispatchManagementBeforeStyle>
    </LoadingWrap>
  );
}
