import * as React from "react";
import styled from "styled-components";
import { useImmer } from "use-immer";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router";
import dotbtnvertical from "../../../assets/img/dotbtnvertical.svg";
import { DEPTH, PATH, URLPATH } from "../../../assets/data/common";
import {
  CostValueForm,
  PointBoxCompo,
  ProductInfo,
  TruckInfo,
  GroupInfo,
  MemoInfo,
} from "../../../components/pages";
import PortalModal from "../../../components/modal/Portal";
import { Confirm } from "../../../components/modal/custom";
import {
  orderManagementAcceptClickRow,
  orderManagementLoading,
  orderManagementClickRowSetInit,
  orderManagementAcceptModify,
} from "../../../store/reducers/orderManagementSlice";
import { defaultGetApiSet, defaultDelApiSet } from "../../../utils/api";
import { s3ConverData } from "../../../utils/s3Convert";
import LoadingWrap from "../../../components/loading/LoadingWrap";

const Styled = {
  OrderManagementBeforeDetail: styled.div``,
  PageHeader: styled.div`
    background-color: #fff;
    padding: 60px 100px 40px 100px;
    border-bottom: 1px solid #e3e3e3;
    .header-wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .header-title {
        font-size: 1.9rem;
        color: #5786ed;
        font-weight: bold;
      }
      .header-link {
        font-size: 1rem;
        color: #ff7474;
        text-decoration: underline;
      }
    }
    .header-more-btn-wrap {
      position: relative;

      .header-more {
        border: none;
        border-radius: 50%;
        text-indent: -9999px;
        background: url(${dotbtnvertical}) no-repeat center;
        :hover {
          background-color: #e3e3e3;
        }
        width: 40px;
        height: 40px;
      }

      &.on .btn-menu-list {
        visibility: visible;
      }

      .btn-menu-list {
        position: absolute;
        right: 0;
        border: 1px solid #e3e3e3;
        border-radius: 5px;
        background-color: #fff;
        visibility: hidden;
        z-index: ${DEPTH.middle};

        & > li {
          padding: 10px 18px;
          white-space: nowrap;
          min-width: 143px;

          a {
            display: block;
            width: 100%;
            height: 100%;
            font-size: 0.9rem;
            color: #b5b5b5;
          }

          &:hover {
            background-color: #eef3fd;
          }
          &:hover a {
            color: #5786ed;
          }
        }
      }
    }
    .header-subtitle {
      font-size: 1rem;
      line-height: 19px;
      color: #939393;
      margin-top: 6px;
    }
  `,
};

const initState = {
  loading: false,
  getData: {},
  goodsImg: [],
  driverImg: [],
  companyLogo: null,
};

export default function OrderManagementBeforeDetail() {
  const [state, setState] = useImmer(initState);
  const ref = React.useRef();
  const openMenuRef = React.useRef(false);
  const [modalVisible, setModalVisible] = React.useState(false);
  const dispatch = useDispatch();
  const history = useHistory();
  const inquiryTarget = useSelector(
    (state) => state.orderManagement.clickTableRow.beforeShip
  );

  const pageData = state.getData;

  const headerLink = (e) => {
    e.preventDefault();
    dispatch(orderManagementAcceptClickRow({ row: inquiryTarget }));
    history.push(PATH.orderManagement_accept_findingDrivers);
  };
  const onToggleMenu = () => {
    openMenuRef.current = !openMenuRef.current;
    setState((draft) => {
      draft.openMenu = openMenuRef.current;
    });
  };
  const onModify = (e) => {
    e.preventDefault();
    dispatch(orderManagementAcceptClickRow({ row: inquiryTarget }));
    dispatch(orderManagementAcceptModify({ row: state.getData }));
    history.push(PATH.orderManagement_accept_findingDriversModify);
  };

  const handleClickOutside = (e) => {
    const target = e.target;
    if (openMenuRef.current && ref.current && !ref.current.contains(target)) {
      openMenuRef.current = false;
      setState((draft) => {
        draft.openMenu = openMenuRef.current;
      });
    }
  };
  const setStartData = async () => {
    let goodsImgStore = [];
    let driverImgStore = [];

    if (!inquiryTarget) {
      history.push(PATH.orderManagement_before);
      return;
    }
    dispatch(orderManagementLoading(true));
    const res = await defaultGetApiSet(
      "",
      `${URLPATH.orderDetail}/${inquiryTarget.order_id}`
    );

    for (const item of res.data.files?.file_id.split(",")) {
      await s3ConverData(res.data.files.s3_folder, item).then((i) => {
        goodsImgStore = [...goodsImgStore, i];
      });
    }

    await s3ConverData(
      res.data.driver_photo?.s3_folder,
      res.data.driver_photo?.pu_photo_id
    )
      .then((i) => {
        driverImgStore = [...driverImgStore, i];
      })
      .catch((e) => {
        driverImgStore = [...driverImgStore, null];
      });
    await s3ConverData(
      res.data.driver_photo?.s3_folder,
      res.data.driver_photo?.do_photo_id
    )
      .then((i) => {
        driverImgStore = [...driverImgStore, i];
      })
      .catch((e) => {
        driverImgStore = [...driverImgStore, null];
      });

    const companyLogo =
      res.data.cargo &&
      (await s3ConverData(res.data.cargo.s3_folder, res.data.cargo.file_id));

    setState((draft) => {
      draft.getData = res.data;
      draft.companyLogo = companyLogo;
      draft.goodsImg = goodsImgStore;
      draft.driverImg = driverImgStore;
    });
    dispatch(orderManagementLoading(false));
  };

  React.useEffect(() => {
    setStartData();
    return () => {
      setState((draft) => {
        draft = initState;
      });
      dispatch(orderManagementClickRowSetInit());
    };
  }, []);
  const deleteAllocated = async () => {
    try {
      const offerId = state.getData.offer_id;
      const path = `${URLPATH.orderOffer}/${offerId}`;
      await defaultDelApiSet("", path);
      setModalVisible(false);
      history.push(PATH.orderManagement_accept_detail);
    } catch (err) {}
  };
  const modalCont = {
    confirm: (
      <Confirm
        title="Are you sure you want to cancel your quote?"
        info="If you cancel a quote, you cannot get it back?"
        CancelSomethingBtnText="Keep quote"
        doSomethingBtnText="Cancel a Quote"
        doSomethingColor="#FF2424"
        doSomething={deleteAllocated}
        onCancel={() => {
          setModalVisible(false);
        }}
      />
    ),
  };
  return (
    <LoadingWrap pageSlice="orderManagement">
      {modalVisible && (
        <PortalModal visible={modalVisible} setVisible={setModalVisible}>
          {modalCont.confirm}
        </PortalModal>
      )}
      <Styled.OrderManagementBeforeDetail>
        <Styled.PageHeader>
          <div className="header-wrap">
            <h2 className="header-title">Before shipping</h2>
            <div
              className={`header-more-btn-wrap ${state.openMenu ? "on" : ""}`}
              ref={ref}
            >
              <button className="header-more" onClick={onToggleMenu}>
                more
              </button>
              <ul className="btn-menu-list">
                <li>
                  <a
                    href="#"
                    onClick={() => {
                      setModalVisible(true);
                    }}
                  >
                    Order Cancel
                  </a>
                </li>
                <li>
                  <a href="#" onClick={onModify}>
                    Allocation Modify
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div className="header-subtitle">#{pageData.order_number}</div>
        </Styled.PageHeader>
        <PointBoxCompo
          pu_city={pageData.pu_city}
          pu_addr={pageData.pu_addr}
          pu_addr_detail={pageData.pu_addr_detail}
          pu_datetime={pageData.pu_datetime}
          pu_pic={pageData.pu_pic}
          pu_phone={pageData.pu_phone}
          do_city={pageData.do_city}
          do_addr={pageData.do_addr}
          do_addr_detail={pageData.do_addr_detail}
          do_datetime={pageData.do_datetime}
          do_pic={pageData.do_pic}
          do_phone={pageData.do_phone}
          company_name={pageData.cargo?.company_name}
        />

        <ProductInfo
          productInfo={pageData.goods}
          goodsImagePath={state.goodsImg}
          driverImagePath={state.driverImg}
        />
        <TruckInfo
          truckType={pageData.truck_type_en}
          truckCount={pageData.truck_count}
          option={pageData.truck_option_en}
        />
        {pageData.memo && <MemoInfo memo={pageData.memo} />}
        <GroupInfo order_id={pageData.order_id} />
        <CostValueForm modifyMode={false} costValue={pageData.price} />
      </Styled.OrderManagementBeforeDetail>
    </LoadingWrap>
  );
}
