import React, { useState, useEffect, useReducer } from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import { useHistory } from "react-router";
import PageLabel from "../../../components/PageLabel";
import { Select } from "../../../components/search";
import { PATH, STATUS, URLPATH } from "../../../assets/data/common";
import CommonFilterCompo from "../../../components/CommonFilterCompo";
import CheckTable from "../../../components/table/CheckTable";
import Pagination from "../../../components/pagination";
import { VehicleManagementCol } from "../../../assets/data/vehicleManagement/tableData";
import { useDispatch } from "react-redux";
import {
  vehicleManagementLoading,
  vehicleManagementClickRow,
} from "../../../store/reducers/vehicleManagementSlice";
import { defaultDelApiSet, defaultGetApiSet } from "../../../utils/api";
import {
  truckListReducer,
  truckTypesReducer,
  truckOptionsReducer,
} from "../../../hooks/useReducer";
import { calcTotalPage } from "../../../utils/convertData";
import PortalModal from "../../../components/modal/Portal";
import { Confirm } from "../../../components/modal/custom";
const Styled = {
  VehicleManagementStyle: styled.div`
    padding-top: 50px;
    .page-label-wrap {
      padding: 30px 100px;
      border-bottom: 1px solid #e3e3e3;
      background-color: #f7f7f7;
    }

    .page-body {
      .table-cont {
        .search-result {
          font-size: 14px;
          color: rgba(124, 124, 124, 0.5);
          padding: 15px 100px;
          background-color: #fff;
        }
      }
    }
  `,
  RegistorBtn: styled.button`
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 0.9rem;
    font-weight: bold;
    background-color: #939393;
    border: none;
    color: #fff;
    border-radius: 5px;

    &:hover {
      background-color: #5786ed;
    }
  `,
  DeleteBtn: styled.button`
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 0.9rem;
    font-weight: bold;
    background-color: #ff2424;
    border: none;
    color: #fff;
    border-radius: 5px;

    &:disabled {
      background-color: #b5b5b5;
      cursor: unset;
    }
  `,

  PagenationStyle: styled(Pagination)`
    justify-content: flex-end;
    margin: 46px 100px 77px 0;
  `,
};

const initState = {
  statusSelect: null,
  modelTypeSelect: null,
  optionSelect: null,
  checkList: [],
  modal: {
    content: "",
    visible: false,
  },
};
export default function VehicleManagement() {
  const [state, setState] = useImmer(initState);
  const [searchDisabled, setSearchDisabled] = useState(true);
  const [pageCount, setPageCount] = useState(10);
  const [truckList, truckListDispatch] = useReducer(truckListReducer, {
    data: [],
    pageInfo: {
      totalData: "",
      currentPage: "",
    },
  });

  const [truckTypes, truckTypesDispatch] = useReducer(truckTypesReducer, []);
  const [truckOptions, truckOptionsDispatch] = useReducer(
    truckOptionsReducer,
    []
  );

  const dispatch = useDispatch();
  const history = useHistory();
  const getTruckList = async (params) => {
    const path = URLPATH.logisTruck;
    try {
      const res = await defaultGetApiSet(params, path);
      truckListDispatch(res.data);
    } catch (err) {}
  };
  const onSearch = () => {
    if (!!state.checkList.length) return;
    const params = {
      count: 10,
      state: state.statusSelect,
      type_id: state.modelTypeSelect,
      opt_id: state.optionSelect,
    };
    getTruckList(params);
  };
  const onReset = () => {
    setState((draft) => {
      draft.statusSelect = initState.statusSelect;
      draft.modelTypeSelect = initState.modelTypeSelect;
      draft.optionSelect = initState.optionSelect;
    });

    const params = {
      count: pageCount,
      page: 1,
    };
    setSearchDisabled(true);
    getTruckList(params);
  };
  const clickPageNum = (page) => {
    const params = {
      count: pageCount,
      page: page,
    };
    getTruckList(params);
  };

  const changeSelect = (key) => async (value) => {
    setState((draft) => {
      draft[key] = value;
    });
    if (key === "modelTypeSelect") {
      const path = URLPATH.logisOrderOptionTpyeList;
      try {
        const res = await defaultGetApiSet({ type_id: value }, path);
        truckOptionsDispatch(res.data);
      } catch (err) {}
    }
  };

  const clickRow = (row) => {
    dispatch(vehicleManagementClickRow({ row }));
    history.push(PATH.vehicleManagement_detail);
  };

  const getCheckList = (value) => {
    setState((draft) => {
      draft.checkList = value;
    });
  };

  const goRegistration = () => {
    history.push(PATH.vehicleManagement_registration);
  };

  const Init = async () => {
    const truckListPath = URLPATH.logisTruck;
    const truckTypePath = URLPATH.logisOrderTruckTypeList;
    const params = {
      count: 10,
      state: state.statusSelect,
      type_id: state.modelTypeSelect,
      opt_id: state.optionSelect,
    };
    try {
      const res = await Promise.all([
        defaultGetApiSet(params, truckListPath),
        defaultGetApiSet("", truckTypePath),
      ]);
      truckListDispatch(res[0].data);
      truckTypesDispatch(res[1].data);
    } catch (err) {}
  };
  useEffect(() => {
    if (
      state.statusSelect !== null ||
      state.modelTypeSelect !== null ||
      state.optionSelect
    ) {
      setSearchDisabled(false);
    }
    // if (state.date.from !== null && state.date.to !== null) {
    // }
  }, [state]);
  useEffect(() => {
    Init();
  }, []);

  const statusOption = [
    {
      key: 0,
      value: STATUS.vehicle[0],
    },
    {
      key: 1,
      value: STATUS.vehicle[1],
    },
    {
      key: 2,
      value: STATUS.vehicle[2],
    },
    {
      key: 3,
      value: STATUS.vehicle[3],
    },
  ];

  const SelectCompo = [
    <Select
      options={statusOption}
      label="Status"
      width="200px"
      onChange={changeSelect("statusSelect")}
      reset={state.statusSelect === null}
      status="status"
    />,
    <Select
      options={truckTypes}
      label="Model Type"
      width="240px;"
      onChange={changeSelect("modelTypeSelect")}
      reset={state.modelTypeSelect === null}
    />,
    <Select
      options={truckOptions}
      label="Option"
      width="240px;"
      onChange={changeSelect("optionSelect")}
      reset={state.optionSelect === null}
      disabled={state.modelTypeSelect === null}
    />,
  ];

  const modalVisible = (isOpen) => {
    setState((draft) => {
      draft.modal.visible = isOpen;
    });
  };

  const modalChange = (cont) => {
    setState((draft) => {
      draft.modal.content = cont;
    });
  };
  const onDelete = () => {
    modalChange("delete");
    modalVisible(true);
  };

  const deleteDriver = async () => {
    try {
      dispatch(vehicleManagementLoading(true));
      const path = URLPATH.logisTruck;
      const body = state.checkList;
      await defaultDelApiSet(body, path);
      modalVisible(false);
      dispatch(vehicleManagementLoading(false));
      window.location.reload();
    } catch (e) {
      modalVisible(false);
      dispatch(vehicleManagementLoading(false));
    }
  };
  const modalCont = {
    delete: (
      <Confirm
        title="Are you sure you want to delete your driver?"
        info="If you delete a driver, the driver will not be visible in the driver management."
        doSomethingBtnText="Delete"
        doSomethingColor="#FF2424"
        doSomething={deleteDriver}
        onCancel={() => modalVisible(false)}
      />
    ),
  };
  const ActionBtnComp = [
    <Styled.RegistorBtn onClick={goRegistration}>+ Add</Styled.RegistorBtn>,
    <Styled.DeleteBtn
      disabled={!state.checkList.length}
      onClick={!!state.checkList.length ? onDelete : () => {}}
    >
      Delete
    </Styled.DeleteBtn>,
  ];
  const totalPage = calcTotalPage(
    truckList.pageInfo.totalData,
    truckList.pageInfo.dataPerPage
  );
  return (
    <Styled.VehicleManagementStyle>
      {state.modal.visible && (
        <PortalModal visible={state.modal.visible} setVisible={modalVisible}>
          {modalCont[state.modal.content]}
        </PortalModal>
      )}
      <div className="page-label-wrap">
        <PageLabel imgType="icon_vehicle" labelText="Vehicle Management" />
      </div>
      <div className="page-body">
        <CommonFilterCompo
          selectCompo={SelectCompo}
          actionBtnComp={ActionBtnComp}
          onSearch={onSearch}
          onReset={onReset}
          disabled={searchDisabled}
        />
        <div className="table-cont">
          <div className="search-result">
            Showing {truckList.pageInfo.currentPage} page of{" "}
            {totalPage ? totalPage : "0"} result
          </div>
          <CheckTable
            columns={VehicleManagementCol}
            data={truckList && truckList.data}
            onClick={clickRow}
            getCheckList={getCheckList}
            page="vehicle"
            headAlign="left"
            bodyAlign="left"
          />
        </div>
      </div>
      {truckList.length !== 0 ? (
        <Styled.PagenationStyle
          currentPage={truckList && truckList.pageInfo.currentPage}
          totalData={truckList && truckList.pageInfo.totalData}
          cb={clickPageNum}
        />
      ) : null}
    </Styled.VehicleManagementStyle>
  );
}
