import * as React from "react";
import styled from "styled-components";
import { useImmer } from "use-immer";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { PATH, URLPATH } from "../../../assets/data/common";
import {
  CostValueForm,
  PointBoxCompo,
  ProductInfo,
  TruckInfo,
  GroupInfo,
  SignCompo,
  MemoInfo,
} from "../../../components/pages";
import {
  orderManagementAcceptClickRow,
  orderManagementLoading,
  orderManagementClickRowSetInit,
} from "../../../store/reducers/orderManagementSlice";
import { defaultGetApiSet, defaultPutApiSet } from "../../../utils/api";
import map from "../../../assets/img/map.svg";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import { s3ConverData } from "../../../utils/s3Convert";

const Styled = {
  OrderManagementAccountDetail: styled.div``,
  PageHeader: styled.div`
    background-color: #fff;
    padding: 60px 100px 40px 100px;
    border-bottom: 1px solid #e3e3e3;
    .header-wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .header-link {
        display: block;
        width: 122px;
        height: 40px;
        line-height: 40px;
        background-color: #5786ed;
        text-align: center;
        color: #fff;
        border-radius: 5px;
      }
    }
    .header-title {
      font-size: 1.9rem;
      color: #ff9b45;
      font-weight: bold;

      &.completed {
        color: #5786ed;
      }
    }
    .header-subtitle {
      font-size: 1rem;
      line-height: 19px;
      color: #939393;
      margin-top: 6px;
    }
  `,
};

const initState = {
  loading: false,
  getData: {},
  goodsImg: [],
  driverImg: [],
  pickUpSignImg: null,
  dropOffSignImg: null,
};

export default function OrderManagementAccountDetail({ pageType }) {
  const [state, setState] = useImmer(initState);
  const dispatch = useDispatch();
  const history = useHistory();
  const inquiryTarget = useSelector(
    (state) => state.orderManagement.clickTableRow.account
  );

  const pageData = state.getData;

  const headerLink = async (e) => {
    e.preventDefault();
    try {
      await defaultPutApiSet(
        "",
        `${URLPATH.orderDetail}/${inquiryTarget.order_id}/complete`
      );
      history.push(PATH.orderManagement_account_completed);
    } catch (e) {
      console.log("error", e);
    }
  };

  const setStartData = async () => {
    let goodsImgStore = [];
    let driverImgStore = [];

    if (!inquiryTarget) {
      history.push(PATH.orderManagement_account);
      return;
    }
    dispatch(orderManagementLoading(true));
    try {
      const res = await defaultGetApiSet(
        "",
        `${URLPATH.orderDetail}/${inquiryTarget.order_id}`
      );

      for (const item of res.data.files?.file_id.split(",")) {
        await s3ConverData(res.data.files.s3_folder, item).then((i) => {
          goodsImgStore = [...goodsImgStore, i];
        });
      }

      await s3ConverData(
        res.data.driver_photo?.s3_folder,
        res.data.driver_photo?.pu_photo_id
      )
        .then((i) => {
          driverImgStore = [...driverImgStore, i];
        })
        .catch((e) => {
          driverImgStore = [...driverImgStore, null];
        });
      await s3ConverData(
        res.data.driver_photo?.s3_folder,
        res.data.driver_photo?.do_photo_id
      )
        .then((i) => {
          driverImgStore = [...driverImgStore, i];
        })
        .catch((e) => {
          driverImgStore = [...driverImgStore, null];
        });

      const pickUp = await s3ConverData(
        res.data.signatures.s3_folder,
        res.data.signatures.pu_signature_id
      );
      const dropOff = await s3ConverData(
        res.data.signatures.s3_folder,
        res.data.signatures.do_signature_id
      );

      setState((draft) => {
        draft.getData = res.data;
        draft.pickUpSignImg = pickUp;
        draft.dropOffSignImg = dropOff;
        draft.goodsImg = goodsImgStore;
        draft.driverImg = driverImgStore;
      });
      dispatch(orderManagementLoading(false));
    } catch (err) {}
  };

  const pageHeaderCompo = (pageType) => {
    switch (pageType) {
      case "waiting":
        return (
          <Styled.PageHeader>
            <div className="header-wrap">
              <h2 className="header-title waiting">Account Waiting</h2>
              <a href="#" className="header-link" onClick={headerLink}>
                Completed
              </a>
            </div>
            <div className="header-subtitle">#{pageData.order_number}</div>
          </Styled.PageHeader>
        );
      case "completed":
        return (
          <Styled.PageHeader>
            <h2 className="header-title completed">Completed</h2>
            <div className="header-subtitle">#{pageData.order_number}</div>
          </Styled.PageHeader>
        );
      default:
        return;
    }
  };

  React.useEffect(() => {
    setStartData();
    return () => {
      setState((draft) => {
        draft = initState;
      });
      dispatch(orderManagementClickRowSetInit());
    };
  }, [pageType]);

  return (
    <LoadingWrap pageSlice="orderManagement">
      <Styled.OrderManagementAccountDetail>
        {pageHeaderCompo(pageType)}
        <PointBoxCompo
          pu_city={pageData.pu_city}
          pu_addr={pageData.pu_addr}
          pu_addr_detail={pageData.pu_addr_detail}
          pu_datetime={pageData.pu_datetime}
          pu_pic={pageData.pu_pic}
          pu_phone={pageData.pu_phone}
          do_city={pageData.do_city}
          do_addr={pageData.do_addr}
          do_addr_detail={pageData.do_addr_detail}
          do_datetime={pageData.do_datetime}
          do_pic={pageData.do_pic}
          do_phone={pageData.do_phone}
          company_name={pageData.cargo?.company_name}
        />

        <ProductInfo
          productInfo={pageData.goods}
          goodsImagePath={state.goodsImg}
          driverImagePath={state.driverImg}
        />
        <TruckInfo
          truckType={pageData.truck_type_en}
          truckCount={pageData.truck_count}
          option={pageData.truck_option_en}
        />
        {pageData.memo && <MemoInfo memo={pageData.memo} />}
        <GroupInfo order_id={pageData.order_id} />
        <SignCompo
          pickUpSign={state.pickUpSignImg}
          pickUpTime={
            state.getData.signatures &&
            state.getData.signatures.pu_signature_created
          }
          dropOffSign={state.dropOffSignImg}
          dropOffTime={
            state.getData.signatures &&
            state.getData.signatures.do_signature_created
          }
        />
        <CostValueForm modifyMode={false} costValue={pageData.price} />
      </Styled.OrderManagementAccountDetail>
    </LoadingWrap>
  );
}
