import * as React from "react";
import styled from "styled-components";
import { useImmer } from "use-immer";
import { useHistory } from "react-router";
import Table from "../../../components/table/Table";
import Pagination from "../../../components/pagination";
import { OrderRequestAcceptCol } from "../../../assets/data/orderManagement/tableData";
import { tableInfo_Accept } from "../../../assets/data/dummy/orderManagement";
import { OrderFilterWrap } from "../../../components/pages";
import { PATH, STATUS, URLPATH } from "../../../assets/data/common";
import { useDispatch } from "react-redux";
import {
  orderManagementAcceptClickRow,
  orderManagementAcceptModify,
  orderManagementLoading,
} from "../../../store/reducers/orderManagementSlice";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import { defaultGetApiSet } from "../../../utils/api";
import { calcTotalPage } from "../../../utils/convertData";
import { createCipher } from "crypto";
const DispatchManagementAcceptStyle = styled.div`
  background-color: #fff;
  .search-result {
    font-size: 14px;
    color: rgba(124, 124, 124, 0.5);
    padding: 15px 100px;
    background-color: #fff;
  }
`;

const PagenationStyle = styled(Pagination)`
  margin-top: 46px;
  justify-content: flex-end;
  margin-right: 100px;
`;

const initState = {
  pageData: {
    orders: [],
    pageInfo: {
      totalData: null,
      currentPage: null,
      dataPerPage: null,
      totalPages: null,
      startRow: null,
    },
  },
  date: {
    from: null,
    to: null,
  },
  status: null,
};

export default function OrderManagementAccept() {
  const [state, setState] = useImmer(initState);
  const history = useHistory();
  const dispatch = useDispatch();

  const clickRow = async (row) => {
    dispatch(orderManagementAcceptClickRow({ row }));
    switch (row.state.toString()) {
      case "0":
        return history.push(PATH.orderManagement_accept_findingDrivers);
      case "1":
        return history.push(PATH.orderManagement_accept_detail);
      case "2":
        try {
          const path = URLPATH.orderDetail;
          const res = await defaultGetApiSet("", `${path}/${row.order_id}`);
          dispatch(orderManagementAcceptModify({ row: res.data }));
        } catch (err) {}
        return history.push(PATH.orderManagement_accept_findingDriversModify);
      default:
        break;
    }
  };
  const clickPagination = async (page) => {
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
      filter: state.status,
      page: page,
    };
    orderRequestApi(req);
  };
  const resetApi = async () => {
    const req = {
      count: 10,
    };
    orderRequestApi(req);
  };
  const onSearch = async () => {
    const req = {
      count: 10,
      from: state.date.from,
      to: state.date.to,
      filter: state.status,
    };
    orderRequestApi(req);
  };

  const orderRequestApi = async (req) => {
    dispatch(orderManagementLoading(true));
    const res = await defaultGetApiSet(req, URLPATH.orderAccept);
    setState((draft) => {
      draft.pageData = res.data;
    });

    dispatch(orderManagementLoading(false));
  };

  const totalPage = calcTotalPage(
    state.pageData.pageInfo.totalData,
    state.pageData.pageInfo.dataPerPage
  );

  React.useEffect(() => {
    const req = {
      count: 10,
    };
    orderRequestApi(req);
  }, []);

  return (
    <LoadingWrap pageSlice="orderManagement">
      <DispatchManagementAcceptStyle>
        <OrderFilterWrap
          isSelect
          selectOption={[
            {
              key: 0,
              value: "Finding Drivers",
            },
            {
              key: 1,
              value: "Allocated",
            },
            {
              key: 2,
              value: "Driver Denied",
            },
          ]}
          setState={setState}
          state={state}
          onSearch={onSearch}
          resetApi={resetApi}
        />
        <div className="search-result">
          Showing{" "}
          {state.pageData.pageInfo.currentPage
            ? state.pageData.pageInfo.currentPage
            : "0"}{" "}
          page of {totalPage ? totalPage : "0"} result
        </div>
        <Table
          data={state.pageData.orders}
          columns={OrderRequestAcceptCol}
          headAlign="left"
          bodyAlign="left"
          onClick={clickRow}
          page="order_accept"
        />
        <PagenationStyle
          currentPage={state.pageData.pageInfo.currentPage}
          totalData={state.pageData.pageInfo.totalData}
          cb={clickPagination}
        />
      </DispatchManagementAcceptStyle>
    </LoadingWrap>
  );
}
