import React, { useEffect, useState } from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import { URLPATH } from "../../../assets/data/common";
import { garageManagementCol } from "../../../assets/data/garageManagement/tableData";
import { garageFormList } from "../../../assets/data/garageManagement/formData";
import PageLabel from "../../../components/PageLabel";
import Table from "../../../components/table/Table";
import Pagination from "../../../components/pagination";
import PortalModal from "../../../components/modal/Portal";
import { FormModal, Confirm } from "../../../components/modal/custom";
import {
  defaultPostApiSet,
  defaultGetApiSet,
  defaultDelApiSet,
  defaultPutApiSet,
} from "../../../utils/api";
import { garageManagementMenuList } from "../../../assets/data/garageManagement";
const Styled = {
  Root: styled.div`
    padding-top: 50px;
    .page-label-wrap {
      padding: 30px 100px;
      border-bottom: 1px solid #e3e3e3;
      background-color: #f7f7f7;
    }
    .page-body {
      .table-cont {
        .search-result {
          font-size: 14px;
          color: rgba(124, 124, 124, 0.5);
          padding: 15px 100px;
          background-color: #fff;
        }
      }
    }
    .addButtonWrapper {
      padding-left: 100px;
      margin: 20px auto;
      .addButton {
        border: none;
        border-radius: 5px;
        background-color: #004cf2;
        color: white;
        font-weight: bold;
        width: 82px;
        height: 40px;
      }
    }
  `,

  PagenationStyle: styled(Pagination)`
    justify-content: flex-end;
    margin: 46px 100px 77px 0;
  `,
};
const initState = {
  garages: [],
  pageInfo: {},
  modal: {
    content: "",
    visible: false,
  },
  clicked: {},
};
export default function GarageManagement() {
  const [state, setState] = useImmer(initState);

  const modifyGarage = async (formDataList) => {
    try {
      const path = `${URLPATH.garage}/${state.clicked.garage_id}`;
      const formData = new FormData();
      formData.append("name", formDataList.name);
      formData.append("address", formDataList.address);
      formData.append("capacity", formDataList.capacity);
      await defaultPutApiSet(formData, path);
      Init();
    } catch (err) {}
  };

  const addGarage = async (formDataList) => {
    try {
      const path = `${URLPATH.garage}`;
      const formData = new FormData();
      formData.append("name", formDataList.name);
      formData.append("address", formDataList.address);
      formData.append("capacity", formDataList.capacity);
      await defaultPostApiSet(formData, path);
      Init();
    } catch (err) {}
  };
  const modalVisible = (isOpen) => {
    setState((draft) => {
      draft.modal.visible = isOpen;
    });
  };

  const modalChange = (content) => {
    setState((draft) => {
      draft.modal.content = content;
    });
  };
  const onDelete = async () => {
    try {
      const path = `${URLPATH.garage}/${state.clicked.garage_id}`;
      await defaultDelApiSet("", path);
      setState((draft) => {
        draft.modal.visible = false;
      });
    } catch {}
    Init();
  };
  const onModal = (content) => (event) => {
    modalChange(content);
    modalVisible(true);
  };
  const onCancel = () => {
    setState((draft) => {
      draft.modal.visible = false;
    });
  };
  const getGarageList = async (params) => {
    try {
      const path = URLPATH.garage;
      const res = await defaultGetApiSet(params, path);
      setState((draft) => {
        res.data.garages.forEach((item) => {
          item.result = `${item.capacity - item.usage} / ${
            item.capacity
          } Trucks`;
        });
        draft.garages = res.data.garages;
        draft.pageInfo = res.data.pageInfo;
      });
    } catch (err) {}
  };

  const Init = async () => {
    const params = {
      count: 10,
      page: 1,
    };
    modalVisible(false);
    getGarageList(params);
  };

  const clickPageNum = (page) => {
    const params = {
      count: 10,
      page: page,
    };
    getGarageList(params);
  };

  useEffect(() => {
    Init();
  }, []);

  const modalCont = {
    add: (
      <FormModal
        title="Enter The Garage Information"
        formList={garageFormList}
        seccesLabel="Add"
        goSubmit={addGarage}
        onCancel={onCancel}
      />
    ),
    delete: (
      <Confirm
        title="Are you sure you want to delete your Garage?"
        info="If you delete a Garage, the Garage will not be visible in the vehicle management."
        doSomethingBtnText="Delete"
        doSomethingColor="#FF2424"
        doSomething={onDelete}
      />
    ),
    modify: (
      <FormModal
        title="Enter The Garage Information"
        formList={garageFormList}
        seccesLabel="Confirm"
        goSubmit={modifyGarage}
        onCancel={onCancel}
        initValue={state.clicked}
      />
    ),
  };
  const tableClick = (item, idx) => {
    setState((draft) => {
      draft.clicked = item;
    });
  };

  return (
    <Styled.Root>
      {state.modal.visible && (
        <PortalModal visible={state.modal.visible} setVisible={modalVisible}>
          {modalCont[state.modal.content]}
        </PortalModal>
      )}
      <div className="page-label-wrap">
        <PageLabel imgType="icon_garage" labelText="Garage Management" />
      </div>
      <div className="page-body">
        <div className="table-cont">
          <div className="search-result">
            Showing{" "}
            {state.pageInfo.currentPage ? state.pageInfo.currentPage : "0"} page
            of {state.pageInfo.totalPages ? state.pageInfo.totalPages : "0"}{" "}
            result
          </div>
          <Table
            columns={garageManagementCol}
            data={state.garages}
            page="garage"
            headAlign="left"
            bodyAlign="left"
            onClick={tableClick}
            menuList={garageManagementMenuList}
            onDelete={onModal("delete")}
            onModifyModal={onModal("modify")}
          />
          <div className="addButtonWrapper">
            <button className="addButton" onClick={onModal("add")}>
              +Add
            </button>
          </div>
        </div>
      </div>
      <Styled.PagenationStyle
        currentPage={state.pageInfo.currentPage}
        totalData={state.pageInfo.totalData}
        cb={clickPageNum}
      />
    </Styled.Root>
  );
}
