import React, { useState, useEffect, useReducer } from "react";
import { useImmer } from "use-immer";
import styled from "styled-components";
import { useHistory } from "react-router";
import PageLabel from "../../../components/PageLabel";
import { Select } from "../../../components/search";
import { PATH, STATUS, URLPATH } from "../../../assets/data/common";
import CommonFilterCompo from "../../../components/CommonFilterCompo";
import CheckTable from "../../../components/table/CheckTable";
import Pagination from "../../../components/pagination";
import { DriverManagementCol } from "../../../assets/data/driverManagement/tableData";
import { useSelector, useDispatch } from "react-redux";
import {
  driverManagementClickRow,
  driverManagementLoading,
} from "../../../store/reducers/driverManagementSlice";
import { defaultDelApiSet, defaultGetApiSet } from "../../../utils/api";
import { drvierListReducer } from "../../../hooks/useReducer";
import { calcTotalPage, transformDriverData } from "../../../utils/convertData";
import LoadingWrap from "../../../components/loading/LoadingWrap";
import PortalModal from "../../../components/modal/Portal";
import { Confirm } from "../../../components/modal/custom";

const Styled = {
  DriverManagementStyle: styled.div`
    padding-top: 50px;
    .page-label-wrap {
      padding: 30px 100px;
      border-bottom: 1px solid #e3e3e3;
      background-color: #f7f7f7;
    }

    .page-body {
      .table-cont {
        .search-result {
          font-size: 14px;
          color: rgba(124, 124, 124, 0.5);
          padding: 15px 100px;
          background-color: #fff;
        }
      }
    }
  `,
  RegistorBtn: styled.button`
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 0.9rem;
    font-weight: bold;
    background-color: #939393;
    border: none;
    color: #fff;
    border-radius: 5px;

    &:hover {
      background-color: #5786ed;
    }
  `,
  DeleteBtn: styled.button`
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 0.9rem;
    font-weight: bold;
    background-color: #ff7474;
    border: none;
    color: #fff;
    border-radius: 5px;

    &:disabled {
      background-color: #b5b5b5;
      cursor: unset;
    }
  `,
  PagenationStyle: styled(Pagination)`
    justify-content: flex-end;
    margin: 46px 100px 77px 0;
  `,
};

const initState = {
  pageData: {
    orders: [],
    pageInfo: {
      totalData: null,
      currentPage: null,
      dataPerPage: null,
      totalPages: null,
      startRow: null,
    },
  },
  status: null,
  checkList: [],
  modal: {
    visible: false,
    contetn: "",
  },
};

export default function DriverManagement() {
  const [state, setState] = useImmer(initState);
  const [searchDisabled, setSearchDisabled] = React.useState(true);
  const dispatch = useDispatch();
  const history = useHistory();
  const pageCount = 10;

  const getDriverList = async (params) => {
    const path = URLPATH.logisDriver;
    try {
      dispatch(driverManagementLoading(true));
      const res = await defaultGetApiSet(params, path);
      setState((draft) => {
        draft.pageData = transformDriverData(res.data);
      });
      dispatch(driverManagementLoading(false));
    } catch (err) {
      console.log("error", err);
      dispatch(driverManagementLoading(false));
    }
  };
  const onSearch = () => {
    if (!!state.checkList.length) return;
    const params = {
      count: pageCount,
      state: state.status,
    };
    getDriverList(params);
  };
  const onReset = () => {
    setState((draft) => {
      draft.status = initState.status;
    });
    setSearchDisabled(true);
    const params = {
      count: pageCount,
      page: 1,
    };
    getDriverList(params);
  };
  const clickPageNum = (page) => {
    const params = {
      count: pageCount,
      page: page,
    };
    getDriverList(params);
  };

  const changeSelect = async (value) => {
    setState((draft) => {
      draft.status = value;
    });
  };

  const clickRow = (row) => {
    dispatch(driverManagementClickRow({ row }));
    history.push(PATH.driverManagement_detail);
  };

  const getCheckList = (value) => {
    setState((draft) => {
      draft.checkList = value;
    });
  };

  const goRegistration = () => {
    history.push(PATH.driverManagement_registration);
  };

  const goDelete = async () => {
    try {
      dispatch(driverManagementLoading(true));
      const req = state.checkList;
      const params = {
        count: pageCount,
        page: 1,
      };
      await defaultDelApiSet(req, URLPATH.logisDriver);
      await getDriverList(params);
      visibleModal(false);
      dispatch(driverManagementLoading(false));
    } catch (e) {
      dispatch(driverManagementLoading(false));
      console.log("error", e);
    }
  };

  const deleteModalOpen = () => {
    visibleModal(true);
    changeModal("deleteConfirm");
  };

  const visibleModal = (visible) => {
    setState((draft) => {
      draft.modal.visible = visible;
    });
  };

  const changeModal = (content) => {
    setState((draft) => {
      draft.modal.content = content;
    });
  };

  const modalCancel = () => {
    visibleModal(false);
  };

  useEffect(() => {
    if (state.status !== null) {
      setSearchDisabled(false);
    }
  }, [state.status]);
  useEffect(() => {
    const params = {
      count: 10,
      state: state.status,
    };
    getDriverList(params);
  }, []);

  const statusOption = [
    {
      key: 0,
      value: STATUS.driver[0],
    },
    {
      key: 1,
      value: STATUS.driver[1],
    },
    {
      key: 2,
      value: STATUS.driver[2],
    },
  ];

  const SelectCompo = [
    <Select
      options={statusOption}
      label="Status"
      width="200px"
      onChange={changeSelect}
      reset={state.status === null}
      status="status"
    />,
  ];
  const ActionBtnComp = [
    <Styled.RegistorBtn onClick={goRegistration}>Registor</Styled.RegistorBtn>,
    <Styled.DeleteBtn
      disabled={!state.checkList.length}
      onClick={!!state.checkList.length ? deleteModalOpen : () => {}}
    >
      Delete
    </Styled.DeleteBtn>,
  ];

  const modalCont = {
    deleteConfirm: (
      <Confirm
        title="Are you sure you want to delete this driver?"
        info="If you delete a truck, the truck will not be visible in the vehicle 
    management."
        doSomethingBtnText="Delete"
        doSomethingColor="#FF7474"
        doSomething={goDelete}
        onCancel={modalCancel}
      />
    ),
  };

  const totalPage = calcTotalPage(
    state.pageData.pageInfo.totalData,
    state.pageData.pageInfo.dataPerPage
  );

  return (
    <LoadingWrap pageSlice="driverManagement">
      {state.modal.visible && (
        <PortalModal visible={state.modal.visible} setVisible={visibleModal}>
          {modalCont[state.modal.content]}
        </PortalModal>
      )}
      <Styled.DriverManagementStyle>
        <div className="page-label-wrap">
          <PageLabel imgType="icon_driver" labelText="Driver Management" />
        </div>
        <div className="page-body">
          <CommonFilterCompo
            selectCompo={SelectCompo}
            actionBtnComp={ActionBtnComp}
            onSearch={onSearch}
            onReset={onReset}
            disabled={searchDisabled}
          />
          <div className="table-cont">
            <div className="search-result">
              Showing{" "}
              {state.pageData.pageInfo.currentPage
                ? state.pageData.pageInfo.currentPage
                : "0"}{" "}
              page of {totalPage ? totalPage : "0"} result
            </div>
            <CheckTable
              columns={DriverManagementCol}
              data={state.pageData.drivers}
              onClick={clickRow}
              getCheckList={getCheckList}
              page="driver"
              headAlign="left"
              bodyAlign="left"
            />
          </div>
        </div>
        <Styled.PagenationStyle
          currentPage={state.pageData.pageInfo.currentPage}
          totalData={state.pageData.pageInfo.totalData}
          cb={clickPageNum}
        />
      </Styled.DriverManagementStyle>
    </LoadingWrap>
  );
}
